import React from 'react';
import Hidden from '@material-ui/core/Hidden';
import AmiMiniHeader from './AmiMiniHeader';
import AmiLargeHeader from './AmiLargeHeader';
    import { GoogleLogout } from 'react-google-login';
  
const AmiHeader = () => (
  <>
  <Hidden xsUp implementation="css"><GoogleLogout

    className="logout"
      clientId="143719263118-jmhigagqibb2ec9hj2p8b1ospkfbe844.apps.googleusercontent.com"
    onLogoutSuccess={()=>console.log('logedout')}
  >
  </GoogleLogout></Hidden>
    <Hidden smUp>
      <AmiMiniHeader
        menus={[
          {
            label: 'NEW RELEASES',
            children: [
              {
                label: 'Insecticides',
                children: [
                  {
                    label: 'green product 1',
                  },
                  {
                    label: 'green product 2',
                  },
                  {
                    label: 'green product 3',
                  },

                ],
              },
              {
                label: 'Organic',
                children: [
                  {
                    label: 'green product 1',
                  },
                  {
                    label: 'green product 2',
                  },
                  {
                    label: 'green product 3',
                  },
                ],
              },
              {
                label: 'Fertilsers',
                children: [
                  {
                    label: 'green product 1',
                  },
                  {
                    label: 'green product 2',
                  },
                  {
                    label: 'green product 3',
                  },
                ],
              },
            ],
          },
          { label: 'Cat3' },
          { label: 'Cat2' },
          { label: 'Caat1' },
        ]}
      />
    </Hidden>


    <Hidden only={'xs'}>
      <AmiLargeHeader />
    </Hidden>

  </>
);

AmiHeader.propTypes = {};
AmiHeader.defaultProps = {};

export default AmiHeader;
