import React, {useState} from 'react';
import {cumulativeOffSet} from "../../utilities/cumulativeOffset"

import { Carousel } from 'react-responsive-carousel';
import Example from './../../utilities/slides.js'
import './../../utilities/slides.css'
const ProductSlider = (
    {
        images
    }
) => {
    const imageRef = React.createRef();
    const [img, setImg] = useState(images[0]);
    const [aItem, setAItem] = useState(0);


    const handleImageChange = (e) => {
        const currentX = e.clientX - cumulativeOffSet(imageRef.current).left;

        console.dir(imageRef.current);

        const part = imageRef.current.clientWidth / images.length;
        console.log(Math.ceil(currentX / part) - 1);

        let imgIndex = Math.ceil(currentX / part) - 1;
        if (imgIndex < 0) {
            imgIndex = 0;
        }

        if (imgIndex >= images.length) {
            imgIndex = images.length - 1;
        }
        setAItem(imgIndex);
        setImg(images[imgIndex]);
    };

    const handleMouseOut = (e) => {
        setImg(images[0]);
        setAItem(0);
    };

    const changeImage = (i) => {
        setImg(images[i]);
        setAItem(i);
    }

    var imageSlides=[]

    images.map(item=>{
      imageSlides.push({original:item,thumbnail: item})
    })


    return (
        <aside className="col-sm-10 ">
            <article className="gallery-wrap">
                <div className="img-big-wrap">

              <Example images={ imageSlides}/>

                </div>
            </article>
        </aside>
    );
};

export default ProductSlider;
