import React, { Component }  from 'react';
// import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { ThemeProvider } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';

import Typography from './components/extensions/Typography';


import theme from './theme';

const url =
  // eslint-disable-next-line max-len
  'https://fonts.googleapis.com/css?family=Poppins:200,400,500,700,900|Oswald:400,700&display=swap';

Typography.setSecondFamily("'Oswald', sans-serif");

  class AmigoHome extends Component {
    constructor(props) {
            super(props);
            this.state={
              product:{
                images:[],
                title:'',
                pricing:{
                  price:0,
                  MRP:0,
                  priceVLE:0,
                },
                details:'',
                description:''
              }
            }
        }



render(){
      return  (

         <ThemeProvider theme={theme}>
           <>

               <Box
                 bgcolor={'#232F3E'}
                 textAlign={'center'}

                 pt={5}
                 pb={5}
               >

            <center>  <Grid container xs={12} direction="row"
  justify="center"
  >
<Grid item lg={3} xs={6}   >
<List dense={true}>
        <ListItem>
<Typography variant={'title'} style={{color:'white'}}>
<b>Get to know us</b>
</Typography>
        </ListItem>
                 <ListItem >
                   <ListItemText
                     primary="Carrers"
style={{color:'white'}}
                   />
                 </ListItem>
                 <ListItem >
                   <ListItemText
                     primary="About us"
style={{color:'white'}}
                   />
                 </ListItem>
                 <ListItem >
                   <ListItemText
                     primary="Press release"
style={{color:'white'}}
                   />
                 </ListItem>

             </List>
</Grid>
<Grid item lg={3} xs={6}>
  <List dense={true}>
        <ListItem>
<Typography variant={'title'} style={{color:'white'}}>
<b>Connect with us</b>
</Typography>
        </ListItem>
                 <ListItem>
                   <ListItemText
                     primary="Facebook"
style={{color:'white'}}
                   />
                 </ListItem>
                 <ListItem>
                   <ListItemText
                     primary="Twitter"
style={{color:'white'}}
                   />
                 </ListItem>
                 <ListItem>
                   <ListItemText
                     primary="Instagram"
style={{color:'white'}}
                   />
                 </ListItem>

             </List>
</Grid>

<Grid item lg={3} xs={6}>
  <List dense={true}>
        <ListItem>
  <Typography variant={'title'} style={{color:'white'}}>
  <b>Make money with us</b>
  </Typography>
        </ListItem>
                 <ListItem>
                   <ListItemText
                     primary="Become a Seller"
  style={{color:'white'}}
                   />
                 </ListItem>
                 <ListItem>
                   <ListItemText
                     primary="Become a VLE"
  style={{color:'white'}}
                   />
                 </ListItem>


             </List>
</Grid>
<Grid item lg={3} xs={6}>
  <List dense={true}>
        <ListItem>
  <Typography variant={'title'} style={{color:'white'}}>
  <b>Contact us</b>
  </Typography>
        </ListItem>
                 <ListItem>
                   <ListItemText
                     primary="Primary address come here"
  style={{color:'white'}}
                   />
                 </ListItem>

             </List>
</Grid>
                </Grid></center>

               </Box>

           </>
         </ThemeProvider>
       );}
  }




export default (AmigoHome);
