
import React, { Component }  from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import CssBaseline from '@material-ui/core/CssBaseline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import { connect } from 'react-redux'
import {GoogleLogin} from 'react-google-login'
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField';
import axios from 'axios'
import AddressEdit from './address'
import IconButton from '@material-ui/core/IconButton';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";

import {
   setOrder,
   UpdateUser
 } from './../../actions/index'
const styles = theme => ({
main:
{
width: 'auto',
display: 'block', // Fix IE 11 issue.
marginLeft: theme.spacing.unit * 3,
marginRight: theme.spacing.unit * 3,
[theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
  width: 400,
  marginLeft: 'auto',
  marginRight: 'auto',
},
},

logo1:{
    width:'60%',
    height:'auto',

},
logo2:{
    width:'40%',
},


paper:
 {
marginTop: theme.spacing.unit * 8,
display: 'flex',
flexDirection: 'column',
alignItems: 'center',
position:'relative',
padding: `${theme.spacing.unit * 10}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
},

avatar: {
width:'150px',
height:'150px',

},

form:
{
width: '100%', // Fix IE 11 issue.
marginTop: theme.spacing.unit,
},

text:{
    position:'absolute',
    top:'30px',
    left:'80px'
},
submit: {
marginTop: theme.spacing.unit * 3,
},
infobox:{
  marginTop:'30px',
   border:'1px solid #8bc34a',

}
});



    class SignIn extends Component {
        constructor(props) {
                super(props);
                this.state={
edit:false

                }
            }

handleAddressDelete=()=>{
  var addressBook=this.props.addressBook
  addressBook.splice(this.props.index,1)
  console.log(addressBook)
  this.props.UpdateUser({addressBook})
    console.log(this.state)
      axios.post(`https://green-planet.herokuapp.com/public/user/updateProfile`,{...this.props.Auth.user}).then((res)=>{
        console.log(res.data)
      })
      localStorage.setItem('Auth', JSON.stringify({...this.props.Auth}))
}

          render() {
          console.log(this.props.mode)
const { classes } = this.props;

return (
<>


    {!this.state.edit&&<Paper elevation={2}>
<div style={{paddingBottom:'50px',padding:'20px',margin:'10px',marginBottom:'30px',position:'relative'}} >
{
this.props.order.addIndex==this.props.index&&
  <CheckCircleIcon color='primary' style={{position:'absolute',top:'10px',left:0}}/>

}
    <Grid container spacing={2} lg={12} alignItems={'center'}  >

  <Grid container spacing={3} justify={'left'}>
    <Grid item xs={12} lg={12}><div style={{position:'absolute',top:0,right:0}}> <IconButton onClick={()=>{this.setState({edit:true})}}><CreateIcon/></IconButton><IconButton onClick={this.handleAddressDelete}><DeleteIcon/></IconButton></div></Grid>
  <Grid item xs={12} lg={12}>
<Typography variant={'subtitle'} style={{textTransform:'capitalize',fontWeight:'500'}} >{this.props.address.name }</Typography>
<Typography variant={'subtitle'} style={{textTransform:'capitalize',fontWeight:'500',marginLeft:'20px'}} >{this.props.address.phone}</Typography>

  </Grid>
  <Grid item xs={12} lg={12}>
<Typography variant={'body'} color={'textSecondary'}>{this.props.address.address+', '+this.props.address.locality+', '+this.props.address.city+', '+this.props.address.state+' - '+this.props.address.postal}</Typography>
  </Grid>
  </Grid>

{
this.props.order.addIndex!=this.props.index&&this.props.mode=='checkOut'&&
  <Grid item xs={12}><Button variant='outlined' fullWidth color='primary' onClick={()=>{this.props.setOrder({addIndex:this.props.index,address:{...this.props.address}})}}>Ship to this address</Button></Grid>
}
</Grid>
</div>
</Paper>}

{this.state.edit&&
<AddressEdit index={this.props.index} edit={this.state.edit} address={this.props.address} close={()=>{this.setState({edit:false})}}/>


}






     </>



);
          }
}

SignIn.propTypes = {
classes: PropTypes.object.isRequired,
};
const mapStateToProps = state => {
    return {
 Auth:state.Auth,
        order:state.order,
        isAuth: state.Auth.isAuth,
        addressBook:state.Auth.user.addressBook
    }
}


export default withRouter(connect(mapStateToProps, {
UpdateUser,
    setOrder

})(withStyles(styles)(SignIn)));
