import {store} from '../App'
import axios from 'axios'




export function updateCart(data,state){
  var payload=[]


  for(var i=0;i<data.length;i++){
    payload.push({
      product:data[i].product._id,
      quantity:data[i].quantity
    })
  }

  axios.post(`https://green-planet.herokuapp.com/public/user/updateCart`,{_id:state.Auth.user._id,cart:payload}).then((res)=>{
    console.log(res.data)
})
  console.log(payload)

}

export function getCart(_id){
  axios.post('https://green-planet.herokuapp.com/public/user/getCart',{_id}).then((res)=>{
    console.log(res.data)
if(res.data.data.cart){
console.log("got")
store.dispatch({type: 'SET_CART', payload:res.data.data.cart})
}

  })
}
