import React from 'react';
import {connect} from 'react-redux';
import {formatMoney} from "../../pipes/priceFormatter";
import Rating from '@material-ui/lab/Rating';
import {addProductToCart} from "../../actions";
import Hidden from "@material-ui/core/Hidden"
import Grid from "@material-ui/core/Grid"
import LocalShippingOutlinedIcon from '@material-ui/icons/LocalShippingOutlined';
import {

    withRouter
  } from "react-router-dom";
  import Button from '@material-ui/core/Button'
  import Typography from '@material-ui/core/Typography'
const ProductDetail = (props) => {

    const {
        title,
        images,
        caseSize,
        defaultShipping,
      sku,
      pricing,
      description,
      _id
    } = props.product;


    const onCart = () => {
        props.addProductToCart(props.product);
        props.history.push('/cart')
    };

    return (
        <aside className="col-sm-12">
            <article className="card-body ">
                <h3 className="title mb-3">{title}</h3>

                  <Rating name="size-medium" readOnly value={4.4} precision={0.1}  /><br/>
                  <Typography variant={'caption'}>Average rating (4.4)</Typography>

<br/>
<br/>
                <p className="price-detail-wrap">
                {pricing.MRP!=undefined&&pricing.MRP>0&&pricing.MRP>pricing.price?
              <div> <span className="price h6 text-warning">
                		<span className="currency">MRP Rs. </span><span className="num"><strike>{formatMoney(pricing.MRP)}</strike></span>
                	</span><br/> </div>:null }
	<span className="price h3 text-warning">
		<span className="currency">Rs. </span><span className="num">{formatMoney(pricing.price)}</span>
	</span><span className="price h6 text-warning"> only</span><br/>
{	props.Auth.authType=='VLE'&&<div><span className="price h6 text-success">
<span className="currency">Rs. </span><span className="num">{formatMoney(pricing.priceVLE)}</span>
</span><span className="price  text-success"> for VLE</span></div>}
                </p>
                <dl className="item-property">
                    <dt>Description</dt>
                    <dd><p className="text-capitalize">{description}</p></dd>
                </dl>

                <hr/>
            <Grid container>
            <Grid item xs={2}><LocalShippingOutlinedIcon/></Grid>
            <Grid item xs={10}><p className="text-capitalize"><small>Free Shipping on quantity more than <b>{caseSize}</b> else shipping charges of Rs. {defaultShipping} (approx) per item will be collected by delivery boy</small> </p></Grid>
            </Grid>
                <Hidden smDown>
                  <Button variant='contained' fullWidth color='primary'
                style={{color:'white',padding:'10px',fontSize:'20px'}}
                      onClick={onCart}
                    ><i
                      className="fa fa-shopping-cart" style={{marginRight:'20px'}}/> Add to cart
                  </Button>
                </Hidden>



            </article>
        </aside>

    );
};

const mapStateToProps = (state) =>  {

  return {

    category: state.common.config.category,
    Auth:state.Auth
  }

};
export default withRouter(connect(mapStateToProps,{addProductToCart})(ProductDetail));
