import React from 'react';
// import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { ThemeProvider } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import Hidden from '@material-ui/core/Hidden';
import Typography from './components/extensions/Typography';
import AmiLargeHeader from './components/header';
import CartItem from "./../components/Order/Order";
import Button from "@material-ui/core/Button"
import ProductAds from './components/ProductAds';
import ProductCard from './components/ProductCard';
import Expander from './components/Expander';
import CategoryPicker from './components/CategoryPicker';

import Pagination from "material-ui-flat-pagination";
import EmailInput from './components/EmailInput';
import MobileSelector from './components/MobileSelector';
import theme from './theme';
import Pages from '../pages/ShopingCart/ShoppingCart'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import Invoice from '../components/pdfInvoice/test'
import { connect } from 'react-redux'
import axios from 'axios'
import {

    withRouter
  } from "react-router-dom";
  // eslint-disable-next-line max-len
var dateFormat = require('dateformat');
Typography.setSecondFamily("'Oswald', sans-serif");

class AmigoHome extends React.Component{
  constructor(props) {
          super(props);
          this.state={
            orders:[],
            offset: 0 ,
            limit:10,
            total:0,
          }
      }

fetchData(offset){
  axios.post('https://green-planet.herokuapp.com/public/user/getOrder',{userId:this.props.user._id,offset:offset,limit:this.state.limit}).then((res,err)=>{
  console.log(res.data)
  if(res.data){
    if(res.data[0].totalData.length>0)
    this.setState({total:res.data[0].totalCount[0].count,orders:res.data[0].totalData})
  }

  })
}

componentDidMount(){
this.fetchData(this.state.offset)
}
handleClick(offset,page) {
  console.log(offset,page)
  this.setState({ offset });
  this.fetchData(offset)
}

render(){

  return (

    <ThemeProvider theme={theme}>
      <>

        <CssBaseline />
        <AmiLargeHeader />
        <Box bgcolor={'common.white'} style={{marginTop:'80px'}}>

          <Box
            mt={{
              xs: -5,
              md: -8,
            }}
mb={{
  lg:4
}}
            position={'relative'}
            zIndex={1}
          >
            <Container fixed>
              <Box
                mx={{
                  xs: -2,
                  sm: 0,
                }}
              >
                <Paper elevation={0}>

                  <Hidden only={'xs'}>
                    <Box pt={2} pb={3} />
                  </Hidden>

                  <Divider light />
                  <Grid container>
                  <Grid item xs={12} sm={8} md={12} lg={12}>
                   <Grid container lg={12} spacing={4} alignItems={'center'} alignContent={'center'}>
{this.state.orders.length==0&&<Grid item xs={12} style={{marginTop:'20vh'}}><center>No orders found</center></Grid>}
{this.state.orders.map(data=>{
    var total=0
return(
  <Grid  item xs={12} sm={12} md={12}>
  <div className="container" >
     <div className="card shopping-cart" style={{minWidth:'80vw'}}>
         <div className="card-header  ">
             <i className="fa fa-shopping-cart pr-2" style={{color:'black'}} aria-hidden="true"></i>
             <strong>OrderId: </strong>{data.txnId}
             <div style={{float:'right'}}><strong>{' '+dateFormat(data.createdAt, "dddd, mmmm dS, yyyy, h:MM:ss TT")} </strong></div>
         </div>
         <div className="card-body">

                { data.products.map((product,i)=>{
                        total=total+(product.quantity*product.price)
                    return(<div><CartItem title={data.product[i].title} description={data.product[i].description} quantity={product.quantity} id={data.product[i]._id} price={product.price} img={data.product[i].images[0]} />
                  <br/><br/></div>)
                })

                }

         </div>
         <div className="card-footer">
         <div className="row align-items-center mb-3">
<div className="col-12 col-sm-12 col-md-6 text-center">
<h6 className="product-name"><strong>Shipping Address</strong></h6>

 <small className="product-description">{data.address.address+', '+data.address.locality+', '+data.address.city+', '+data.address.state+' - '+data.address.postal}</small>
</div>
<div className="col-12 col-sm-12 col-md-6 text-right">
<div className="pull-right" style={{margin: '10px'}}>
                 <div className="pull-right" style={{margin: '5px'}}>
                    <p style={{textAlign:'right'}}>Total price: <i class="fa fa-inr"></i> <b>{total}</b>

                   </p>
<Invoice data={data}/>
                 </div>
             </div>
</div>

</div>

         </div>
     </div>
 </div>
         </Grid>
)
})}






<Grid item sm={12} justifyContent={'center'}>

        <center>  <Pagination
            limit={10}
            offset={this.state.offset}
            total={this.state.total}
            size={'large'}

            onClick={(e, offset,page) => {this.handleClick(offset,page)}}
          /></center>

</Grid>
                      </Grid>

                    </Grid>



                  </Grid>
                </Paper>
              </Box>
            </Container>
          </Box>
          <Box
            bgcolor={'grey.200'}
            textAlign={'center'}
            position={'relative'}
            zIndex={0}
            pt={10}
            pb={5}
          >
            <Container maxWidth={'sm'}>
              <Typography
                spacing={'medium'}
                secondFamily
                weight={500}
                gutterBottom
              >
                SUBSCRIBE TO OUR NEWSLETTER
              </Typography>
              <Typography variant={'caption'} display={'block'}>
                To always stay update with our products, news, and special
                discounts
              </Typography>
              <Typography
                variant={'caption'}
                display={'block'}
                bottomSpace={'medium'}
              >
                enter you email below
              </Typography>
              <EmailInput />
            </Container>
          </Box>
        </Box>
      </>
    </ThemeProvider>
  );
}
}


const mapStateToProps = state => {
    return {

      user: state.Auth.user
    }
}


export default withRouter(connect(mapStateToProps, {


})(AmigoHome));
