import { Snackbar } from "@material-ui/core";
import axios from 'axios'
import {store} from '../App'
export const ADD_PRODUCT_TO_CART = 'ADD_PRODUCT_TO_CART';
export const REMOVE_PRODUCT_FROM_CART = 'REMOVE_PRODUCT_FROM_CART';
export const INCREMENT_CART_ITEM_QUANTITY = 'INCREMENT_CART_ITEM_QUANTITY';
export const DECREMENT_CART_ITEM_QUANTITY = 'DECREMENT_CART_ITEM_QUANTITY';



export const setOrder = data => {
    return {
        type: 'SET_ORDER',
        payload:data
    }
};


export const toggleLoader = () => {
    return {
        type: 'TOGGLE_LOADER',

    }
};

export const setAddress = data => {
    return {
        type: 'SET_ADDRESS',
        payload:data
    }
};

export const setProducts = data => {
    return {
        type: 'SET_PRODUCTS',
        payload:data
    }
};




export const updateProduct = data => {
    return {
        type: 'UPDATE_PRODUCT',
        payload:data
    }
};

export const updateAttachment = data => {
    return {
        type: 'UPDATE_ATTACH',
        payload: data
    }
};


export const addProductToCart = product => {
  const state=store.getState()
    return {
        type: ADD_PRODUCT_TO_CART,
        payload: product,
        state
    }
};
export const setCart = (cart)=> {
console.log('setting cart action')
const state=store.getState()
console.log(cart)
    return {
      state:state,
        type: 'SET_CART',
        payload: cart,

    }
};


export const removeProductToCart = productId => {
    const state=store.getState()

    return {
        type: REMOVE_PRODUCT_FROM_CART,
        payload: productId,
state
    }
};

export const incrementCartQuantity = productId => {
  const state=store.getState()
    return{
        type: INCREMENT_CART_ITEM_QUANTITY,
        payload: productId,
          state
    }
};

export const decrementCartQuantity = productId => {
  const state=store.getState()
  return {
      type: DECREMENT_CART_ITEM_QUANTITY,
      payload: productId,
        state
  }
};


export const ADD_BRAND_TO_FILTER = 'ADD_BRAND_TO_FILTER';
export const REMOVE_BRAND_FROM_FILTER = 'REMOVE_BRAND_FROM_FILTER';

export const addBrandToFilter = brand => {
    return {
        type: ADD_BRAND_TO_FILTER,
        brand
    }
};


export const removeBrandFromFilter = brand => {
    return  {
        type: REMOVE_BRAND_FROM_FILTER,
        brand
    }
};

export const ORDER_BY_ASC = 'ORDER_BY_ASC';
export const ORDER_BY_DESC = 'ORDER_BY_DESC';
export const CLEAR_ORDER_BY_PRICE = 'CLEAR_ORDER_BY_PRICE';

export const orderByAsc = () => {
    return {
        type: ORDER_BY_ASC
    }
};

export const orderByDesc =  () => {
    return {
        type: ORDER_BY_DESC
    }
};

export const clearOrderBy = () => {
    return {
        type: CLEAR_ORDER_BY_PRICE
    }
};


export const PREV_PAGE = 'PREV_PAGE';
export const NEXT_PAGE = 'NEXT_PAGE';
export const GO_PAGE = 'GO_PAGE';
export const COUNT_ITEM = 'COUNT_ITEM';


export const nextPage = () => {
    return {
        type: NEXT_PAGE
    }
};

export const prevPage = () => {
    return {
        type: PREV_PAGE
    }
};

export const goPage = (n) => {
    return {
        type: GO_PAGE,
        currentPage: n
    }
};

export const countItem = (n) => {
    return {
        type: COUNT_ITEM,
        totalItemsCount: n
    }
}

export const snackbar = (snackData) => {
    return {
        type: 'SNACKBAR',
        snackbar: snackData
    }
}

export const config = (Data) => {
      return {
          type: 'SETCONFIG',
          config: Data
      }
    }

export const SignInUser= (user_data)=> {
    console.log(user_data)
    return  {type: 'SET_USER', data:{user:user_data.user,token:user_data.token}}


}
export const logout= (user_data)=> {
  localStorage.setItem('Auth',null)
    console.log(user_data)
    return  {type: 'UNSET_USER'}


}
export const setAuthType= (payload)=> {


    return  {type: 'SetAuthType',payload}


}

export const UpdateUser=(data)=>{

    return  {type: 'update_user', data}
}
