import React, { Component }  from 'react';
// import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { ThemeProvider } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Footer from './footer'
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import Hidden from '@material-ui/core/Hidden';
import Typography from './components/extensions/Typography';
import AmiLargeHeader from './components/header';
import ProductAds from './components/ProductAds';
import ProductCard from './components/ProductCard';
import Expander from './components/Expander';
import CategoryPicker from './components/CategoryPicker';
import Expansion from './productDetails/expansion'
import BrandPicker from './components/BrandPicker';
import ColorPicker from './components/ColorPicker';
import SizePicker from './components/SizePicker';
import Pagination from './components/Pagination';
import EmailInput from './components/EmailInput';
import MobileSelector from './components/MobileSelector';
import theme from './theme';
import Pages from '../pages/ShopingCart/ShoppingCart'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import ProductDetailComponent from './../components/ProductDetail/ProductDetail';
import Ratings from './../components/ProductDetail/rating'
import ProductSlider from "./../components/ProductSlider/ProductSlider";
import {connect} from 'react-redux';
import Spec from './spec'
import {addProductToCart} from "./../actions";
import Button from '@material-ui/core/Button'
import axios from 'axios';
const url =
  // eslint-disable-next-line max-len
  'https://fonts.googleapis.com/css?family=Poppins:200,400,500,700,900|Oswald:400,700&display=swap';

Typography.setSecondFamily("'Oswald', sans-serif");

  class AmigoHome extends Component {
    constructor(props) {
            super(props);
            this.state={
              product:{
                images:[],
                title:'',
                specification:[],
                pricing:{
                  price:0,
                  MRP:0,
                  priceVLE:0,
                },
                details:'',
                description:''
              }
            }
        }

        componentDidMount=()=>{
          axios.get('https://green-planet.herokuapp.com/public/productdetail/'+this.props.match.params.id).then((res)=>{
            console.log(res)
if(res.data){
  this.setState({product:{specification:[],...res.data[0]}})
}


          })
        }

        onCart = () => {
            this.props.addProductToCart(this.state.product);
            this.props.history.push('/cart')
        };

render(){
      return  (

         <ThemeProvider theme={theme}>
           <>
             <Helmet>
               <link href={url} rel="stylesheet" />
               <title>{this.state.product.title}</title>
             </Helmet>
             <CssBaseline />
             <AmiLargeHeader />
             <Box bgcolor={'common.white'} style={{marginTop:'60px'}} >

               <Box

               >
                 <Container >
                   <Box

                   >
                     <Paper elevation={0} style={{marginBottom:'100px'}}>



                           <Grid container spacing={3}  >

                               <Grid  item xs={12} sm={12} md={6} style={{marginTop:'30px'}}>
                               <ProductSlider images={this.state.product.images}/>
                               </Grid>
                               <Grid  item xs={12} sm={12} md={6}>
                               <ProductDetailComponent product={this.state.product}/>
                               </Grid>


                           </Grid>

                           <Grid container  style={{marginTop:'80px'}} >
              

                                <Grid item xs={12}  >
                              <Expansion specification={this.state.product.specification} details={this.state.product.details}/>
                             </Grid>
                           </Grid>
                           <br/>
                           <br/>

                             <Grid container spacing={3} style={{border:'1px solid #f0f0f0'}} >
                             <Grid item xs={12} >
                               <Typography variant={'h6'}> Ratings and Review</Typography><br/><br/>
                             <Ratings productId={this.props.match.params.id}/>
                             </Grid>
                           </Grid>



                     </Paper>
                   </Box>
                 </Container>
               </Box>


             </Box>
             <Hidden smUp>
               <Button variant='contained' fullWidth color='primary'
             style={{color:'white',padding:'15px',fontSize:'20px',position:'fixed',bottom:0,margin:'0px 0px 0px 0px',zIndex:2000,width:'100vw'}}
onClick={this.onCart}
                 ><i
                   className="fa fa-shopping-cart" style={{marginRight:'20px'}}/> Add to cart
               </Button>
             </Hidden>
           </>
         </ThemeProvider>
       );}
  }


AmigoHome.propTypes = {};
AmigoHome.defaultProps = {};


const mapStateToProps = (state, props) =>  {

    const product = state.shop.products.find(product => product.id === +props.match.params.id);

};



export default connect(mapStateToProps,{addProductToCart})(AmigoHome);
