import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import List from './list'
import {Link} from 'react-router-dom'
import ExpandMore from '@material-ui/icons/ExpandMore';

export default function SimpleMenu(props) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div style={{color:'#000',fontWeight:'500',minHeight:'40px'}} >
          {!props.data.children&& <Link to={'/products/category/'+ props.data.title}>
          <Button fullWidth aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick} >

            {props.data.title}

          </Button>
        </Link>

        }

      {props.data.children&& <div><Button fullWidth aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick} >

          {props.data.title}  <ExpandMore/>  </Button>


        <Menu

          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
          elevation={0}
     getContentAnchorEl={null}
     anchorOrigin={{
       vertical: 'bottom',
       horizontal: 'center',
     }}
     transformOrigin={{
       vertical: 'top',
       horizontal: 'center',
     }}
        >
      <List tree={props.data.children}/>
        </Menu></div> 
      }

    </div>
  );
}
