import React from 'react';
// import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { ThemeProvider } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import Hidden from '@material-ui/core/Hidden';
import Typography from './components/extensions/Typography';
import AmiLargeHeader from './components/header';
import ProfileComp from '../components/Uprofile/login'

import EmailInput from './components/EmailInput';
import MobileSelector from './components/MobileSelector';
import theme from './theme';
import Pages from '../pages/ShopingCart/ShoppingCart'
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
const url =
  // eslint-disable-next-line max-len
  'https://fonts.googleapis.com/css?family=Poppins:200,400,500,700,900|Oswald:400,700&display=swap';

Typography.setSecondFamily("'Oswald', sans-serif");

const AmigoHome = (props) => (
  <>
  <ThemeProvider theme={theme}>
  {console.log(theme.mixins.toolbar.minHeight+'px')}
      <Helmet>
        <link href={url} rel="stylesheet" />
      </Helmet>
      <CssBaseline />
      <AmiLargeHeader />
      <Box bgcolor={'common.white'} style={{paddingTop:'56px',paddingBottom:'30px'}}>

        <Box
          mt={{
            xs: -5,
            md: -8,
          }}

          position={'relative'}
          zIndex={1}
        >
          <Container fixed>
            <Box
              mx={{
                xs: -2,
                sm: 0,
              }}
            >
              <Paper elevation={0}>

                <Hidden only={'xs'}>
                  <Box pt={2} pb={3} />
                </Hidden>



                <ProfileComp/>





              </Paper>
            </Box>
          </Container>
        </Box>
        <Box
          bgcolor={'grey.200'}
          textAlign={'center'}
          position={'relative'}
          zIndex={0}
          pt={10}
          pb={5}
        >
          <Container maxWidth={'sm'}>
            <Typography
              spacing={'medium'}
              secondFamily
              weight={500}
              gutterBottom
            >
              SUBSCRIBE TO OUR NEWSLETTER
            </Typography>
            <Typography variant={'caption'} display={'block'}>
              To always stay update with our products, news, and special
              discounts
            </Typography>
            <Typography
              variant={'caption'}
              display={'block'}
              bottomSpace={'medium'}
            >
              enter you email below
            </Typography>
            <EmailInput />
          </Container>
        </Box>
      </Box>

  </ThemeProvider>
  </>
);

AmigoHome.propTypes = {};
AmigoHome.defaultProps = {};

export default AmigoHome;
