import React, { Component }  from 'react';
// import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';
import { ThemeProvider } from '@material-ui/styles';
import Container from '@material-ui/core/Container';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Divider from '@material-ui/core/Divider';
import Hidden from '@material-ui/core/Hidden';
import Typography from './components/extensions/Typography';
import AmiLargeHeader from './components/header';
import ProductAds from './components/ProductAds';
import ProductCard from './components/ProductCard';
import Expander from './components/Expander';
import CategoryPicker from './components/CategoryPicker';

import BrandPicker from './components/BrandPicker';
import ColorPicker from './components/ColorPicker';
import SizePicker from './components/SizePicker';
import Tabs from '../components/Tabs/tabs'
import EmailInput from './components/EmailInput';
import {toggleLoader} from '../actions/index'
import MobileSelector from './components/MobileSelector';
import theme from './theme';
import axios from 'axios';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import {connect} from 'react-redux';
import Skeleton from '@material-ui/lab/Skeleton';
import Pagination from "material-ui-flat-pagination";
const url =
  // eslint-disable-next-line max-len
  'https://fonts.googleapis.com/css?family=Poppins:200,400,500,700,900|Oswald:400,700&display=swap';

Typography.setSecondFamily("'Oswald', sans-serif");



class AmigoHome extends Component {
  constructor(props) {
          super(props);
          this.state={
            products:[],
            loading:true,
            orders:[],
            offset: 0 ,
            limit:15,
            total:0,
          }
      }

componentDidMount=()=>{
this.fetchData(this.state.offset)
}
handleClick(offset,page) {
  console.log(offset,page)
  this.setState({ offset });
  this.fetchData(offset)
}

    fetchData(offset){
      this.setState({loading:true})
      this.props.toggleLoader()
      axios.post('https://green-planet.herokuapp.com/public/product/getProducts',{offset:offset,limit:this.state.limit}).then((res,err)=>{
      console.log(res.data)
      if(res.data){
          this.props.toggleLoader()
        if(res.data[0].totalData.length>0)
        this.setState({loading:false,total:res.data[0].totalCount[0].count,products:res.data[0].totalData})
      }

      })
    }





  render(){
    return(

   <ThemeProvider theme={theme}>
     <>
       <Helmet>
         <link href={url} rel="stylesheet" />
       </Helmet>
       <CssBaseline />
       <AmiLargeHeader />
       <div style={{marginTop:'64px'}}><Tabs category={this.props.category}/></div>
       <Box bgcolor={'common.white'} >

       <Box
           bgcolor={'#CDD5FF'}
           height={{
             xs: 144,
             md: 430,
           }}

         >
         <Carousel showThumbs={false} infiniteLoop autoPlay>
                 <div>
                     <img src='https://slorge.s3.us-east-2.amazonaws.com/imageedit_2_5758561074.png'/>

                 </div>
                 <div>
                     <img src='https://slorge.s3.us-east-2.amazonaws.com/imageedit_4_3633858446.png'/>

                 </div>
                 <div>
                     <img src='https://slorge.s3.us-east-2.amazonaws.com/imageedit_5_2831891912.png'/>

                 </div>
             </Carousel>

         </Box>
         <Box
           mt={{
             xs: -5,
             md: -8,
           }}

           position={'relative'}
           zIndex={1}
         >
           <Container fixed>
             <Box
               mx={{
                 xs: -2,
                 sm: 0,
               }}
             >
               <Paper elevation={0}>
                 <Grid container>
                   <Grid item xs={12} sm={6} md={5}>
                     <ProductAds
                       {...ProductAds.test1}
                       contentSide={'left'}
                       contentBoxProps={{ maxWidth: 200 }}
                     />
                   </Grid>
                   <Grid item xs={12} sm={6} md={7}>
                     <ProductAds
                       {...ProductAds.test2}
                       contentSide={'right'}
                       contentBoxProps={{ maxWidth: 300 }}
                     />
                   </Grid>
                 </Grid>
                 <Hidden only={'xs'}>
                   <Box pt={2} pb={3} />
                 </Hidden>

                 <Divider light />
                 <Grid container>
                   <Hidden only={'xs'}>
                     <Grid item xs={12} sm={4} md={3} style={{borderLeft:'1px solid #f0f0f0'}}>
                       <Expander label={'CATEGORY'}>
                         <CategoryPicker categories={this.props.category} />
                       </Expander>


                     </Grid>
                   </Hidden>
                   <Grid item xs={12} sm={8} md={9}>
                     <Grid container>
                       {
                         !this.state.loading&&this.state.products.map((data,index) => {

                           return(
                             <Grid key={data.name} item xs={6} sm={6} md={4}>
                               <ProductCard product={data} VLE={this.props.Auth.authType=='VLE'?true:false} bordered /></Grid>
                           )





                       })}

                       {
                          this.state.loading&&[1,2,3,4,5,6,7,8,9].map((data,index) => {

                           return( <Grid key={data.name} item xs={6} sm={6} md={4}>
                             <Box key={index} width='90%' marginRight={0.5} marginLeft={0.5} my={5}>

               <Skeleton variant="rect" width="100%" height={118} style={{padding:'20px'}} />




               <React.Fragment>
                 <Skeleton />
                 <Skeleton width="60%" />
               </React.Fragment>

           </Box>
                         </Grid>
                           )




                       })
                       }
                     </Grid>
                     <center>  <Pagination
                         limit={this.state.limit}
                         offset={this.state.offset}
                         total={this.state.total}
                         size={'large'}

                         onClick={(e, offset,page) => {this.handleClick(offset,page)}}
                       /></center>

                   </Grid>
                 </Grid>
               </Paper>
             </Box>
           </Container>
         </Box>
         <Box
           bgcolor={'grey.200'}
           textAlign={'center'}

           zIndex={0}
           pt={10}
           pb={5}
         >
           <Container maxWidth={'sm'}>
             <Typography
               spacing={'medium'}
               secondFamily
               weight={500}
               gutterBottom
             >
               SUBSCRIBE TO OUR NEWSLETTER
             </Typography>
             <Typography variant={'caption'} display={'block'}>
               To always stay update with our products, news, and special
               discounts
             </Typography>
             <Typography
               variant={'caption'}
               display={'block'}
               bottomSpace={'medium'}
             >
               enter you email below
             </Typography>
             <EmailInput />
           </Container>
         </Box>
       </Box>
     </>
   </ThemeProvider>
   );

  }


}

AmigoHome.propTypes = {};
AmigoHome.defaultProps = {};

const mapStateToProps = (state) =>  {

  return {

    category: state.common.config.category,
    Auth:state.Auth
  }

};



export default connect(mapStateToProps,{toggleLoader})(AmigoHome);
