
const defaultState = {
    appName: '',
    modalMode: false,
    snackbar:
    {open:false,
    sType:'success',
    message:'',}
    ,
    config:{
      category:[],
      state:[]
    },
    drawer:true,
    loader:false
  };

  export default (state = defaultState, action) => {
    switch (action.type) {


       case 'SNACKBAR':
       return{
         ...state,
         snackbar:action.snackbar

       }
       case 'TOGGLE_LOADER':
       return{
         ...state,
         loader:!state.loader

       }
       case 'SETCONFIG':
       return{
         ...state,
         config:action.config

       }
      default:
        return state;
    }
  };
