import React from 'react';
import PropTypes from 'prop-types';
import Box from '@material-ui/core/Box';
import Typography from './extensions/Typography';

const ProductInfo = ({ name, price, inverted, rootBoxProps,VLE }) => (
  <Box px={2} pb={2} {...rootBoxProps}>
    <Typography
      variant={'subtitle1'}
      secondFamily
      weight={'bold'}
      gutterBottom
      inverted={inverted}
    >
      {name}
    </Typography>
    <Typography color={'primary'} weight={'bold'} inverted={inverted}>
      {price.MRP!=undefined&&price.MRP>0&&price.MRP>price.price? <div><i class="fa fa-inr"></i><strike>{" "+price.MRP}</strike>{" "+price.price}</div>
    :  <div><i class="fa fa-inr"></i>{" "+price.price}</div>
    }

    </Typography>
    {VLE&&<Typography color={'primary'} weight={'bold'} inverted={inverted}>
    VLE: <i class="fa fa-inr"></i> {" "+price.priceVLE}
    </Typography>}
  </Box>
);

ProductInfo.propTypes = {
  name: PropTypes.string.isRequired,
  price: PropTypes.number.isRequired,
  inverted: PropTypes.bool,
  rootBoxProps: PropTypes.shape({}),
};
ProductInfo.defaultProps = {
  inverted: false,
  rootBoxProps: {},
  price:{
    MRP:0,
    priceVLE:0,
    price:0
  }
};

export default ProductInfo;
