import {
    ADD_PRODUCT_TO_CART,
    DECREMENT_CART_ITEM_QUANTITY,
    INCREMENT_CART_ITEM_QUANTITY,
    REMOVE_PRODUCT_FROM_CART
} from '../actions';
import {updateCart} from '../actions/sync'

import {phones} from "../data/phones";
import {store} from '../App'

const initialState = {
    products: phones,
    cart: [
    ]
};


const shopReducer = (state = initialState, action ) => {

    let updatedCart;
    let updatedItemIndex;

    switch (action.type) {
case 'SET_CART':
console.log('setting cart')
if(action.state)
updateCart(action.payload,action.state)
return {...state, cart: action.payload};


        case INCREMENT_CART_ITEM_QUANTITY:
            updatedCart = [...state.cart];
            updatedItemIndex = updatedCart.findIndex(
                item => item.product.sku === action.payload
            );

            const incrementedItem = {
                ...updatedCart[updatedItemIndex]
            };

            incrementedItem.quantity++;

            updatedCart[updatedItemIndex] = incrementedItem;
            console.log(action.state)
            if(action.state.Auth.isAuth)
           updateCart(updatedCart,action.state)

            return {...state, cart: updatedCart};

        case DECREMENT_CART_ITEM_QUANTITY:
            updatedCart = [...state.cart];
            updatedItemIndex = updatedCart.findIndex(
                item => item.product.sku === action.payload
            );

            const decrementedItem = {
                ...updatedCart[updatedItemIndex]
            };

            decrementedItem.quantity--;

            updatedCart[updatedItemIndex] = decrementedItem;
            if(action.state.Auth.isAuth)
            updateCart(updatedCart,action.state)
            return {...state, cart: updatedCart};

        case ADD_PRODUCT_TO_CART:
            updatedCart = [...state.cart];
            updatedItemIndex = updatedCart.findIndex(item => item.product.sku === action.payload.sku);

            if(updatedItemIndex < 0) {
                updatedCart.push({product:action.payload, quantity: 1});
            } else {
                const updatedItem = {

                    ...updatedCart[updatedItemIndex]
                };

                updatedItem.quantity++;
                updatedCart[updatedItemIndex] = updatedItem;
            }
            if(action.state.Auth.isAuth)
            updateCart(updatedCart,action.state)
            return {...state, cart: updatedCart};
        case REMOVE_PRODUCT_FROM_CART:
            updatedCart = [...state.cart];
            updatedItemIndex = updatedCart.findIndex(
                item => item.product.sku === action.payload
            );

            updatedCart.splice(updatedItemIndex, 1);
            if(action.state.Auth.isAuth)
            updateCart(updatedCart,action.state)
            return {...state, cart: updatedCart};
        default:
            return state;

    }
};

export default shopReducer;
