
const initialState = {
    user: {

addressBook:[
]
    },
    isAuth:false,
    token:'',
    authType:'',
}

export default (state = initialState, action) => {
  switch (action.type) {
    case 'UNSET_USER':
    return {
        ...state,

        user:{
        addressBook:[]},
       isAuth: false,
        token:null,


    }
    case 'SET_USER':
    console.log(action.data.user)

        return {
            ...state,

            user:action.data.user,
           isAuth: true,
            token:action.data.token||'',


        }
        case 'update_user':
        console.log('updating user...')
        console.log(action.data)
        return{
          ...state,
          user:{
            ...state.user,
            ...action.data
          }
        }


    case 'UNSET_USER':
    console.log('Unsetting user...')
    localStorage.removeItem('Auth')
        return {
            ...state,
            isAuth:false,
            user:{},
            profile:{}
        }
        case 'UpdateProfile':
        console.log('Unsetting user...')

        return {
            ...state,

        }
        case 'SetAuthType':
        return{
          ...state,
          authType:action.payload
        }
    default:
      return state;
  }
}
