import axios from 'axios'

var Promise = require('promise');

export function UploadImg(data){
    return new Promise(function (resolve, reject) {
    const formData = new FormData();
    formData.append('file', data);

    axios.post('https://slorge.herokuapp.com:8080/test-upload', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then(response => {
      
    resolve(response)
    }).catch(error => {
      reject(error)
    });
      });

}
export function UploadEditorImg(data,type){
  return new Promise(function (resolve, reject) {
  const formData = new FormData();
  console.log(type)
  formData.append('file', data);
  formData.append('ext', type.ext);
  formData.append('mime', type.mime);
  axios.post('https://slorge.herokuapp.com/img-upload', formData, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  }).then(response => {
    console.log(response)
  resolve(response)
  }).catch(error => {
    reject(error)
  });
    });

}

export function formatDate(date){
  var d=new Date(date)
        var months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        var dt={}
        dt.day=d.getDate()
        dt.month=months[d.getMonth()]
        dt.year=d.getFullYear()
        return dt
}