import React from 'react';
import PropTypes from 'prop-types';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '././extensions/Typography';
import {Link} from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import {connect} from 'react-redux';
import {

  withRouter
} from "react-router-dom";
const useStyles = makeStyles({
  root: {
    height:316,
    flexGrow: 1,
    maxWidth: 400,
  
  },
});

 function ControlledTreeView(props) {
   console.log(props)
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState([]);

  const renderItem=(data)=>{

    let output=data.map(item=>{
      if(item.children&&item.children.length>0){
        return(

          <TreeItem style={{padding:"5px"}} nodeId={item.title}  label={item.title}>
         { renderItem(item.children)}
          </TreeItem>
        )
      }
      else{
        return(
          <TreeItem nodeId={item.title} style={{padding:"5px"}} onClick={()=>props.history.push('/products/category/'+item.title)} label={item.title}/>
        )
      }

    })

    return output
  }

  const handleChange = (event, nodes) => {
    setExpanded(nodes);
  };
  

  return (
    <TreeView
      className={classes.root}
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpandIcon={<ChevronRightIcon />}
      expanded={expanded}
      onNodeToggle={handleChange}
    >

   {renderItem(props.categories)}
    </TreeView>
  );
}
const CategoryPicker = ({ categories }) => 
{
  console.log(categories)
  return(null)
}

CategoryPicker.propTypes = {
  categories: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      amount: PropTypes.number,
    }),
  ),
};
CategoryPicker.defaultProps = {
  categories: [],
};
CategoryPicker.data = [
  {
    title: 'Green Product 1',
    amount: 56,
  },
  {
    title: 'Green Product 2',
    amount: 1,
    active: true,
  },
  {
    title: 'Green Product 3',
    amount: 23,
  },
  {
    title: 'Green Product 4',
    amount: 40,
  },
  {
    title: 'Green Product 5',
    amount: 11,
  },
  {
    title: 'Green Product 6',
    amount: 2,
  },
  {
    title: 'Green Product 7',
    amount: 8,
  },
  {
    title: 'Green Product 8',
    amount: 15,
  },
  {
    title: 'Green Product 9',
    amount: 14,
  },
];

export default withRouter(connect()(ControlledTreeView))