
import React, { Component }  from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import CssBaseline from '@material-ui/core/CssBaseline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import { connect } from 'react-redux'
import {GoogleLogin} from 'react-google-login'
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid'
import Divider from '@material-ui/core/Divider'
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import axios from 'axios'
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";

import {
   UpdateUser,
   snackbar
 } from './../../actions/index'
const styles = theme => ({
main:
{
width: 'auto',
display: 'block', // Fix IE 11 issue.
marginLeft: theme.spacing.unit * 3,
marginRight: theme.spacing.unit * 3,
[theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
  width: 400,
  marginLeft: 'auto',
  marginRight: 'auto',
},
},

logo1:{
    width:'60%',
    height:'auto',

},
logo2:{
    width:'40%',
},


paper:
 {
marginTop: theme.spacing.unit * 8,
display: 'flex',
flexDirection: 'column',
alignItems: 'center',
position:'relative',
padding: `${theme.spacing.unit * 10}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
},

avatar: {
width:'150px',
height:'150px',

},

form:
{
width: '100%', // Fix IE 11 issue.
marginTop: theme.spacing.unit,
},

text:{
    position:'absolute',
    top:'30px',
    left:'80px'
},
submit: {
marginTop: theme.spacing.unit * 3,
},
infobox:{
  marginTop:'30px',
   border:'1px solid #8bc34a',

}
});



    class SignIn extends Component {
        constructor(props) {
                super(props);
                this.state={

                  name:'',
                	address: '',
                	locality:'',

                	landmark:'',
                	city:'',
                	state: '',
                	postal: 0,
                  stateCode:0,
                	phone: 0,

                }
            }

handleSave=()=>{
if(this.props.edit){
  var addressBook=this.props.addressBook
  addressBook[this.props.index]={...this.state}
  console.log(addressBook)
  this.props.UpdateUser({addressBook})
    console.log(this.state)
      axios.post(`https://green-planet.herokuapp.com/public/user/updateProfile`,{...this.props.Auth.user,addressBook}).then((res)=>{
        console.log(res.data)
      })
      localStorage.setItem('Auth', JSON.stringify({...this.props.Auth}))
    this.props.close()
}
  else{
    var add=this.props.addressBook
    add.push(this.state)
    console.log(add)
  this.props.UpdateUser({addressBook:add})
    console.log(this.state)
      axios.post(`https://green-planet.herokuapp.com/public/user/updateProfile`,{...this.props.Auth.user}).then((res)=>{
        console.log(res.data)
      })
      localStorage.setItem('Auth', JSON.stringify({...this.props.Auth}))
    this.props.close()

  }

}
componentDidMount=()=>{
  console.log(this.props)
  if(this.props.edit){
    this.setState({...this.props.address})
  }
}

          render() {
              //if(this.props.isAuth){
               //   this.props.history.push('/')
             // }

const { classes } = this.props;

return (
<>


  <Grid container spacing={2} justify={'center'} alignItems={'center'} >
  <Grid item xs={12}>
  <Typography variant='subtitle1' color='primary'>{this.props.index>-1?'UPDATE ADDRESS':'ADD AN ADDRESS'}</Typography>
  <Divider light />
  </Grid>
  <Grid item xs={12} lg={6} >
<TextField
        id="outlined-name"
        label="Name"
onChange={(e)=>{this.setState({name:e.target.value})}}
        margin="normal"
        value={this.state.name}
        variant="outlined"
      />
</Grid>
<Grid item xs={12} lg={6}>
<TextField
        id="outlined-name"
        label="Mobile number"
type='number'
  onChange={(e)=>{this.setState({phone:e.target.value})}}
        margin="normal"
        variant="outlined"
        value={this.state.phone}
      />
</Grid>
<Grid item xs={12} lg={6} >
<TextField
        id="outlined-name"
        label="Pincode"
      type='number'
      onChange={(e)=>{this.setState({postal:e.target.value})}}
        margin="normal"
        value={this.state.postal}
        variant="outlined"
      />
</Grid>
<Grid item xs={12} lg={6} >
<TextField
        id="outlined-name"
        label="Locality"
value={this.state.locality}
onChange={(e)=>{this.setState({locality:e.target.value})}}
        margin="normal"
        variant="outlined"
      />
</Grid>
<Grid item xs={12} lg={12}>
<TextField
          id="outlined-multiline-static"
          label="Address"
          multiline
          onChange={(e)=>{this.setState({address:e.target.value})}}
          rows="3"
value={this.state.address}
        fullWidth

          variant="outlined"

      />
</Grid>
<Grid item xs={12} lg={6}>
<TextField
        id="outlined-name"
        label="City"
value={this.state.city}
    onChange={(e)=>{this.setState({city:e.target.value})}}
        margin="normal"
        variant="outlined"
      />
</Grid><Grid xs={12} item lg={6} >
<FormControl variant="outlined" className={classes.formControl}>
        <InputLabel htmlFor="outlined-age-simple">
          State
        </InputLabel>
        <Select
          value={this.state.stateCode}
      onChange={(e)=>{
        var state=this.props.config.state.filter((data)=>(data.stateCode==e.target.value))
        this.setState({state:state[0].name,stateCode:e.target.value})}}

          inputProps={{
            name: 'state',
            id: 'outlined-age-simple',
          }}
        >

          {this.props.config.state.map(st=>{
            return  (<MenuItem value={st.stateCode} name={st.name}>{st.name}</MenuItem>)
          })}

        </Select>
      </FormControl>

</Grid>
<Grid item xs={6} lg={6} >
<Button fullWidth variant="contained" color="primary" style={{color:'white',padding:'10px',marginTop:'10px'}} onClick={this.handleSave}>
SAVE
</Button>
</Grid>
<Grid item xs={6} lg={6} >
<Button fullWidth variant="contained" color="primary" style={{color:'white',padding:'10px',marginTop:'10px'}} onClick={this.props.close}>
CANCEL
</Button>
</Grid>
  </Grid>
     </>



);
          }
}

SignIn.propTypes = {
classes: PropTypes.object.isRequired,
};
const mapStateToProps = state => {
    return {
 Auth:state.Auth,
        isAuth: state.Auth.isAuth,
        addressBook:state.Auth.user.addressBook,
        config:state.common.config
    }
}


export default withRouter(connect(mapStateToProps, {

    UpdateUser,
    snackbar

})(withStyles(styles)(SignIn)));
