
import React, { Component }  from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import CssBaseline from '@material-ui/core/CssBaseline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import { connect } from 'react-redux'
import {GoogleLogin} from 'react-google-login'
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField';
import AddressComp from './address'
import axios from 'axios'
import Divider from '@material-ui/core/Divider'
import IconButton from '@material-ui/core/IconButton';
import CreateIcon from '@material-ui/icons/Create';
import AddressBlock from './addressBlock'
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";

import {
      UpdateUser,
      snackbar

 } from './../../actions/index'
const styles = theme => ({
main:
{
width: 'auto',
display: 'block', // Fix IE 11 issue.
marginLeft: theme.spacing.unit * 3,
marginRight: theme.spacing.unit * 3,
[theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
  width: 400,
  marginLeft: 'auto',
  marginRight: 'auto',
},
},

logo1:{
    width:'60%',
    height:'auto',

},
logo2:{
    width:'40%',
},


paper:
 {
marginTop: theme.spacing.unit * 8,
display: 'flex',
flexDirection: 'column',
alignItems: 'center',
position:'relative',
padding: `${theme.spacing.unit * 10}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
},

avatar: {
width:'150px',
height:'150px',

},

form:
{
width: '100%', // Fix IE 11 issue.
marginTop: theme.spacing.unit,
},

text:{
    position:'absolute',
    top:'30px',
    left:'80px'
},
submit: {
marginTop: theme.spacing.unit * 3,
},
infobox:{

    margin:'auto',
    marginTop:'70px',
    padding:'20px'
}
});



    class SignIn extends Component {
        constructor(props) {
          super(props);
          this.state={
            addressDia:false,
            editProfile:false,
            firstName:this.props.Auth.user.firstName,
            lastName:this.props.Auth.user.lastName,
            mobile:this.props.Auth.user.mobile
          }

            }

            editProfile=()=>{
              this.props.UpdateUser({firstName:this.state.firstName,lastName:this.state.lastName,mobile:this.state.mobile})
              axios.post(`https://green-planet.herokuapp.com/public/user/updateProfile`,{...this.props.Auth.user,firstName:this.state.firstName,lastName:this.state.lastName,mobile:this.state.mobile}).then((res)=>{
                console.log(res.data)
              })
              localStorage.setItem('Auth', JSON.stringify({...this.props.Auth,user:{...this.props.Auth.user,firstName:this.state.firstName,lastName:this.state.lastName,mobile:this.state.mobile}}))
              this.setState({editProfile:false})
              this.props.snackbar({sType:'success',open:true,message:'Profile edited succesfully'})
            }


          render() {
              //if(this.props.isAuth){
               //   this.props.history.push('/')
             // }
             console.log(this.state.editProfile)

const { classes } = this.props;

return (
<>
  <CssBaseline />

  <Grid container xs={12} md={12} lg={8} justify={'center'} alignItems={'center'} className={classes.infobox}>
  <Grid item xs={12}>
  <center><Avatar className={classes.avatar} src={this.props.Auth.user.provider_pic} /></center>
  {!this.state.editProfile&&<IconButton onClick={()=>this.setState({editProfile:true}) } style={{float:'right'}}><CreateIcon/></IconButton>}
  </Grid>
  <Grid item xs={12}  >
  <Grid container justify={'center'} spacing={2}>
<Grid item >
<TextField
        id="outlined-name"
        label="Firstname"
        value={this.state.firstName}
        InputProps={{
            readOnly:!this.state.editProfile,
          }}
        margin="normal"
        onChange={(e)=>this.setState({firstName:e.target.value})}
        variant="outlined"
      />
</Grid>
<Grid item >
<TextField
        id="outlined-name"
        label="Lastname"
        value={this.state.lastName}
        InputProps={{
            readOnly:!this.state.editProfile,
          }}
        margin="normal"
        onChange={(e)=>this.setState({lastName:e.target.value})}
        variant="outlined"
      />
</Grid>

  </Grid>
  <Grid container justify={'center'} spacing={2}>
<Grid item >
<TextField
        id="outlined-name"
        label="Mobile number"
        InputProps={{
          readOnly:!this.state.editProfile,
          }}
        margin="normal"
        value={this.state.mobile}
        onChange={(e)=>this.setState({mobile:e.target.value})}
        variant="outlined"
      />
</Grid>
<Grid item >
<TextField
        id="outlined-name"
        label="Email"
        type='email'
        InputProps={{
            readOnly: true,
          }}
        margin="normal"
        value={this.props.Auth.user.email}
        variant="outlined"
      />
</Grid>
{this.state.editProfile&&
  <>
  <Grid xs={6} >
    <Button fullWidth color='primary' onClick={this.editProfile}>Save</Button>
  </Grid>
  <Grid xs={6} >
      <Button fullWidth color='primary' onClick={()=>{this.setState({editProfile:false})}}>Cancel</Button>
  </Grid></>
}

  </Grid>


  </Grid>
  <Grid item xs={12}> <Divider style={{marginTop:'50px'}} light/></Grid>
  <Grid item xs={12}>

  <Grid container  justify={'center'} spacing={2} alignItems={'center'} >
<Grid item xs={12} ><Typography variant={'h6'} style={{paddingTop:'20px'}} color='primary' gutterBottom>Address Book</Typography></Grid>
  <Grid item xs={12} >
    <AddressBlock />
  {!this.state.addressDia&&<Button variant="contained" fullWidth color="primary" style={{color:'white',padding:'10px',marginTop:'10px'}} fullWidth onClick={()=>this.setState({addressDia:!this.state.addressDia})}  >
  ADD NEW ADDRESS
  </Button>}
  {this.state.addressDia&&<AddressComp mode={1} data={null} index={-1} close={()=>{this.setState({addressDia:!this.state.addressDia})}}/>}
  </Grid>
  </Grid>


  </Grid>
  </Grid>
     </>



);
          }
}

SignIn.propTypes = {
classes: PropTypes.object.isRequired,
};
const mapStateToProps = state => {
    return {
 Auth:state.Auth,
        isAuth: state.Auth.isAuth
    }
}


export default withRouter(connect(mapStateToProps, {
snackbar,
  UpdateUser,

})(withStyles(styles)(SignIn)));
