import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Grid from '@material-ui/core/Grid';
import withStyles from '@material-ui/core/styles/withStyles';
import ListItemAvatar from '@material-ui/core/ListItemAvatar'
import axios from 'axios'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import {setCart} from '../../actions'
import {updateCart} from '../../actions/sync'
const products = [
  { name: 'Product 1', desc: 'A nice thing', price: '$9.99' },
  { name: 'Product 2', desc: 'Another thing', price: '$3.45' },
  { name: 'Product 3', desc: 'Something else', price: '$6.51' },
  { name: 'Product 4', desc: 'Best thing of all', price: '$14.11' },
  { name: 'Shipping', desc: '', price: 'Free' },
];
const addresses = ['1 Material-UI Drive', 'Reactville', 'Anytown', '99999', 'USA'];
const payments = [
  { name: 'Card type', detail: 'Visa' },
  { name: 'Card holder', detail: 'Mr John Smith' },
  { name: 'Card number', detail: 'xxxx-xxxx-xxxx-1234' },
  { name: 'Expiry date', detail: '04/2024' },
];

const style = theme => ({
  listItem: {
    padding: theme.spacing(1, 0),
  },
  total: {
    fontWeight: '700',
  },
  title: {
    marginTop: theme.spacing(2),
  },
});

class Review extends React.Component  {
  constructor(props) {
    super(props);
    this.state={
      addressDia:false,
      transData:null,
      cart:null,
      status:null,
      orderId:null,


    }
this.handleSuccess=this.handleSuccess.bind(this)
this.openBolt=this.openBolt.bind(this)


      }

componentDidMount(){
  axios.post('https://green-planet.herokuapp.com/public/addorder',{address:this.props.order.address,id:this.props.user._id}).then(res=>{
console.log(res.data)
if(res.data.trans){
  this.setState({transData:res.data.trans,cart:res.data.order.products})
}

  })
}
handleSuccess=()=>{
  console.log('payment success')
}
responseHandler= (BOLT)=>{
  axios.post('http://localhost:8080/public/transaction/hook',{...BOLT.response}).then((data)=>{
    console.log(data)
  })
    console.log( BOLT );
    if(BOLT.response.txnStatus=='SUCCESS'){



this.props.setCart([])
  this.props.handleCheckout()


    }
  }

  catchException=(BOLT)=>{

     console.log( BOLT );

      // "amount: should be numeric"
      // "txnid: mandatory param is missing"
      // "Error in adding payment"

  }
 openBolt=()=>{




              window.bolt.launch(this.state.transData,{responseHandler:this.responseHandler,catchException:this.catchException})

}

render(){
  let total=0

console.log(this.props.authType)
  this.props.cart.map(prd=>{
    if(this.props.authType=='VLE')
    total=total+(prd.product.pricing.priceVLE*prd.quantity)
    else {
          total=total+(prd.product.pricing.price*prd.quantity)
    }
  })




const { classes } = this.props;
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Order summary
      </Typography>
      <List disablePadding>
        {this.props.cart.map(product => (
          <ListItem className={classes.listItem} key={product.product._id}>
           <ListItemAvatar>
<img src={product.product.images[0]} style={{maxHeight:'200px',padding:'10px'}}/>
           </ListItemAvatar>
            <ListItemText primary={product.product.title} secondary={'Qty: ' + product.quantity} />

            <Typography variant="body2">{this.props.authType=='VLE'?product.product.pricing.priceVLE:product.product.pricing.price}</Typography>
          </ListItem>
        ))}
        <ListItem className={classes.listItem}>
          <ListItemText primary="Total" />
          <Typography variant="subtitle1" className={classes.total}>
            {"Rs. "+total}
          </Typography>
        </ListItem>
      </List>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <Typography variant="h6" gutterBottom className={classes.title}>
            Shipping
          </Typography>
          <Typography gutterBottom capitalize>{this.props.order.address.name}</Typography>
          <Typography gutterBottom>{this.props.order.address.address+', '+this.props.order.address.locality+', '+this.props.order.address.city+', '+this.props.order.address.state+' - '+this.props.order.address.postal}</Typography>
        </Grid>
        <Grid item container direction="column" xs={12} sm={6}>
          <Typography variant="h6" gutterBottom className={classes.title}>
            Payment details
          </Typography>
          <Grid container>

              <React.Fragment key={'payment'}>
                <Grid item xs={6}>
                  <Typography gutterBottom>Payment Type</Typography>
                </Grid>
                <Grid item xs={6}>
                  <Typography gutterBottom>Online Payment PayU</Typography>
                </Grid>
              </React.Fragment>

          </Grid>
        </Grid>
        <Grid item xs={6}><Button   variant="contained" color='primary' style={{color:'white',padding:'10px'}} fullWidth onClick={this.props.handleCheckout} >Cancel</Button></Grid>
          <Grid item xs={6}><Button   variant="contained" color='primary' style={{color:'white',padding:'10px'}} fullWidth onClick={this.openBolt}>Proceed to payment</Button></Grid>
      </Grid>
    </React.Fragment>
  );}
}

const mapStateToProps = state => {
    return {
order:state.order,
        cart: state.shop.cart,
        user:state.Auth.user,
        state:state,
        authType:state.Auth.user.type
    }
}





export default (connect(mapStateToProps, {
setCart

}))(withStyles(style)(Review))
