import React from 'react';

import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import InfoIcon from '@material-ui/icons/Info';
import green from '@material-ui/core/colors/green';
import amber from '@material-ui/core/colors/amber';

import Snackbar from '@material-ui/core/Snackbar';

import WarningIcon from '@material-ui/icons/Warning';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux'
import MySnackbarContentWrapper from './snackbarContent'
import {snackbar} from './../../actions/index'
const variantIcon = {
  success: CheckCircleIcon,
  warning: WarningIcon,
  error: ErrorIcon,
  info: InfoIcon,
};

const styles1 = theme => ({
  success: {
    backgroundColor: green[600],
  },
  error: {
    backgroundColor: theme.palette.error.dark,
  },
  info: {
    backgroundColor: theme.palette.primary.dark,
  },
  warning: {
    backgroundColor: amber[700],
  },
  icon: {
    fontSize: 20,
  },
  iconVariant: {
    opacity: 0.9,
    marginRight: theme.spacing.unit,
  },
  message: {
    display: 'flex',
    alignItems: 'center',
  },
});



const styles2 = theme => ({
  margin: {
    margin: theme.spacing.unit,
  },
});

class CustomizedSnackbars extends React.Component {
  state = {
    open: false,
    type:'success',
    content:""
  };

  handleClick = () => {
    this.setState({ open: true });
  };

  handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
console.log('close')
    this.props.snackbar({open:false,sType:'success',message:''})
  };

  render() {
    const { classes } = this.props;

    return (

        <Snackbar
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
          open={this.props.Open}
          autoHideDuration={3000}
          onClose={this.handleClose}
        >
          <MySnackbarContentWrapper
          onClose={this.handleClose}
            variant={this.props.Type}
            message={this.props.Message}
          />
        </Snackbar>

    );
  }
}

const mapStateToProps = state => {
  return {
      Open: state.common.snackbar.open,
      Message:state.common.snackbar.message,
      Type:state.common.snackbar.sType

  }
}
export default connect(mapStateToProps,{snackbar})((withStyles(styles2)(CustomizedSnackbars)));
