
import React, { Component }  from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import { connect } from 'react-redux'
import {GoogleLogin} from 'react-google-login'
import Hidden from '@material-ui/core/Hidden'
import axios from 'axios'
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import {getCart} from '../../actions/sync'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import OtpInput from 'react-otp-input';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";

import {
   SignInUser,
   snackbar
 } from './../../actions/index'
const styles = theme => ({

   paper: {
     marginTop: theme.spacing(8),
     display: 'flex',
     flexDirection: 'column',
     alignItems: 'center',
   },
   avatar: {
     margin: theme.spacing(1),
     backgroundColor: theme.palette.secondary.main,
   },
   form: {
     width: '100%', // Fix IE 11 issue.
     marginTop: theme.spacing(1),
   },
   submit: {
     margin: theme.spacing(3, 0, 2),
   },
});



    class SignIn extends Component {
        constructor(props) {
                super(props);
                this.state={
                  mobile:'',
                  state:2,
                  otp:'',
                  name:'',
                  password:'',
                  cpassword:''
                }
            }

            componentDidMount=()=>{

            }

setOtp=(otp)=>{
  var phoneno = /^\d{1,4}$/;

  if((otp.match(phoneno)&&otp.length<11))
        {
  this.setState({otp:this.state.otp+otp})
}
}


handleChange=(e)=>{
  var phoneno = /^\d{1,10}$/;
  console.log(e.target.value)
  if((e.target.value.match(phoneno)&&e.target.value.length<11))
        {
          this.setState({mobile:e.target.value})
          console.log(this.state.mobile)
        }
  if(e.target.value==''){
    this.setState({mobile:''})
  }

}

handleVerifyOtp=()=>{
this.props.snackbar({open:true,sType:'info',message:'please wait while we are verifying your mobile',})
  axios.post('https://green-planet.herokuapp.com/public/user/verifyUserPassReset',{uid:this.state.mobile,otp:this.state.otp}).then(res=>{
if(res.data.type=='error'){
  this.props.snackbar({open:true,sType:'error',message:'Oops! something went wrong please try again',})
}
if(res.data.type=='not exist'){
  this.props.snackbar({open:true,sType:'info',message:'User with given mobile does not exists',})
  this.setItem({state:2})
}

if(res.data.type=='failed'){
  this.props.snackbar({open:true,sType:'error',message:'Invalid OTP! Please regenerate',})
  this.setItem({state:2})
}

if(res.data.type=='success'){
    this.props.snackbar({open:true,sType:'success',message:'Successfuly verified!Please reset you password',})
    this.setState({state:4})
    let user = res.data.data


    localStorage.setItem('Auth', JSON.stringify({user:user,token:res.data.token}))
this.props.SignInUser({token:res.tokenId,user:user})
getCart(user._id)
}



  })
}

verifyNumber=()=>{
  this.props.snackbar({open:true,sType:'info',message:'Please wait sending OTP for verification',})
  axios.post('https://green-planet.herokuapp.com/public/user/sendotp',{mobile:this.state.mobile}).then(res=>{
    if(res.data.type=='success'){
        this.props.snackbar({open:true,sType:'success',message:'OTP sent!Please verify OTP',})
        this.setState({state:3})
    }
    else{
        this.props.snackbar({open:true,sType:'error',message:'Oops! something went wrong.Please try again',})
    }
  })
}

handleSetPassword=()=>{
  if(this.state.name.length<1){
    this.props.snackbar({open:true,sType:'error',message:'Please enter Valid name',})
}else
if(this.state.password.length<4){
  this.props.snackbar({open:true,sType:'error',message:'Password should be at least 4 character long',})
}else
if(this.state.password!=this.state.cpassword){

    this.props.snackbar({open:true,sType:'error',message:'Password does not match!',})

}
else {
    this.props.snackbar({open:true,sType:'info',message:'Please wait setting your new password',})
    axios.post('https://green-planet.herokuapp.com/public/user/resetPassword',{password:this.state.password,name:this.state.name,_id:this.props.user._id}).then(res=>{
      if(res.data.type=='success'){
          this.props.snackbar({open:true,sType:'success',message:'Password reset success.Welcome to green planet!',})
          this.props.history.push('/')
      }
      else{
        this.props.snackbar({open:true,sType:'error',message:'Oops! something went wrong.Please try again',})
      }
    })
}

}




          render() {

const { classes } = this.props;

return (
<div>
{this.state.state==2&&
  <Container component="main" maxWidth="xs">
     <CssBaseline />
     <div className={classes.paper}>
       <Avatar className={classes.avatar}>
         <LockOutlinedIcon />
       </Avatar>
       <Typography component="h1" variant="h5">
         Forgot Password
       </Typography>
       <form className={classes.form} noValidate>
         <TextField
           variant="outlined"
           margin="normal"
           required
           fullWidth
           id="mobile"
           value={this.state.mobile}
           label="Mobile Number"
           name="mobile"
           onChange={this.handleChange}
           autoComplete="mobile"
           autoFocus
         />


         <Button

           fullWidth
           variant="contained"
           color="primary"
           onClick={this.verifyNumber}
           className={classes.submit}
         >
           Reset Password
         </Button>

       </form>
     </div>

   </Container>

}





   {this.state.state==3&&


     <Container component="main" maxWidth="xs">
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Enter OTP
          </Typography>


            <Grid container  style={{marginTop:'30px'}}>

              <Grid item xs={12}>
              <center> <div>  <OtpInput
                    style={{width:'100%'}}
                 onChange={otp =>this.setState({otp:otp})}
                 numInputs={4}

                 shouldAutoFocus={true}
                 inputStyle={{width:'80px',height:'80px',fontSize:'30px',borderRadius:'10px',boxShadow:'none',border:'2px solid grey'}}
                 separator={<span>-</span>}
               /></div></center>

              </Grid>
            </Grid>


            <Button
            onClick={this.handleVerifyOtp}
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
            >
              Verify OTP
            </Button>
            <Button

                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
                onClick={()=>{this.setState({state:2})}}
              >
                Resend OTP
              </Button>


        </div>

      </Container>}

      {this.state.state==4&&
        <Container component="main" maxWidth="xs">
           <CssBaseline />
           <div className={classes.paper}>
             <Avatar className={classes.avatar}>
               <LockOutlinedIcon />
             </Avatar>
             <Typography component="h1" variant="h5">
               Set Password
             </Typography>
             <form className={classes.form} noValidate>
               <TextField
                 variant="outlined"
                 margin="normal"
                 required
                 fullWidth

                 value={this.state.name}
                 name="Name"
                 label="Name"
                 type="text"
                 id="password"
                 autoComplete="name"
                 onChange={(e)=>{this.setState({name:e.target.value})}}

                 autoFocus
               />

               <TextField
                 variant="outlined"
                 margin="normal"
                 required
                 fullWidth

                 value={this.state.password}
                 name="password"
                 label="Password"
                 type="password"
                 id="password"
                 autoComplete="current-password"
                 onChange={(e)=>{this.setState({password:e.target.value})}}

                 autoFocus
               />
               <TextField
                 variant="outlined"
                 margin="normal"
                 required
                 fullWidth
                 value={this.state.cpassword}
                 onChange={(e)=>this.setState({cpassword:e.target.value})}
                 name="password"
                 label="Confirm Password"
                 type="password"
                 id="password"
                 autoComplete="current-password"
               />

               <Button

                 fullWidth
                 variant="contained"
                 color="primary"
                 onClick={this.handleSetPassword}
                 className={classes.submit}
               >
                 Set Password
               </Button>


             </form>
           </div>

         </Container>
      }
</div>


);
          }
}

SignIn.propTypes = {
classes: PropTypes.object.isRequired,
};
const mapStateToProps = state => {
    return {
    user:state.Auth.user,
        isAuth: state.Auth.isAuth
    }
}


export default withRouter(connect(mapStateToProps, {

    SignInUser,
    snackbar

})(withStyles(styles)(SignIn)));
