import React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import MoneyIcon from '@material-ui/icons/Money';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import CreditCardIcon from '@material-ui/icons/CreditCard';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';

import { connect } from 'react-redux'
import {
   setOrder
 } from './../../actions/index'
class PaymentForm extends React.Component  {
  constructor(props) {
    super(props);
    this.state={

    }
      }


render() {
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Payment method
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12}>
        <List  fullWidth style={{width:'100%'}}>
        <ListItem  button onClick={()=>this.props.setOrder({paymentMode:'COD'})}>
           <ListItemIcon>
            <MoneyIcon color='primary'/>
           </ListItemIcon>
           <ListItemText primary={'cash on delivery'} />
           <ListItemSecondaryAction>

            {  this.props.order.paymentMode=='COD'&&
              <CheckCircleIcon size='large' color='primary'/>
            }

           </ListItemSecondaryAction>
         </ListItem>
         <ListItem  button onClick={()=>this.props.setOrder({paymentMode:'card'})}>
            <ListItemIcon>
             <CreditCardIcon color='primary'/>
            </ListItemIcon>
            <ListItemText primary={'Debit/Credit Card'} />
            <ListItemSecondaryAction>
              {this.props.order.paymentMode=='card'&&
                  <CheckCircleIcon size='large' color='primary'/>}
            </ListItemSecondaryAction>
          </ListItem>
          <ListItem  button onClick={()=>this.props.setOrder({paymentMode:'netBanking'})}>
             <ListItemIcon>
              <AccountBalanceIcon color='primary'/>
             </ListItemIcon>
             <ListItemText primary={'Netbanking'} />
             <ListItemSecondaryAction>
              {  this.props.order.paymentMode=='netBanking'&&
                <CheckCircleIcon size='large' color='primary'/>}
             </ListItemSecondaryAction>
           </ListItem>

   </List>
   </Grid>
 </Grid>
    </React.Fragment>
  );
}
}

const mapStateToProps = state => {
    return {

        order: state.order
    }
}





export default (connect(mapStateToProps, {
setOrder

}))(PaymentForm)
