
const initialState = {
    products: [],
    paymentMode:null,
    address: {},
   addIndex:-1
};


const orderReducer = (state = initialState, action ) => {



    switch (action.type) {
case 'SET_ADDRESS':

return {

  ...state, address: action.payload
};
case 'SET_PRODUCTS':

return {

  ...state, products: action.payload
};

case 'SET_ORDER':

return {

  ...state, ...action.payload
};
        default:
            return state;

    }
};

export default orderReducer;
