import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';
import AppBar from '@material-ui/core/AppBar';
import Box from '@material-ui/core/Box';
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';
import Collapse from '@material-ui/core/Collapse';
import Toolbar from '@material-ui/core/Toolbar';
import Grid from '@material-ui/core/Grid';
import Slide from '@material-ui/core/Slide';
import InputBase from '@material-ui/core/InputBase';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Icon from '../extensions/Icon';
import IconButton from '@material-ui/core/IconButton';
import ButtonM from '@material-ui/core/Button'
import Close from '@material-ui/icons/Close';
import Typography from '../extensions/Typography';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import CategoryPicker from '../CategoryPicker';
import Menu from '@material-ui/core/Menu';
import {connect} from 'react-redux';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import AccountCircle from '@material-ui/icons/AccountCircle';
import {NavLink,Link,withRouter} from 'react-router-dom';
import {logout} from '../../../actions'
const useStyles = makeStyles(({ transitions, palette, spacing }) => ({
  appBarBg: {
    zIndex: 1098,
  },
  searchBox: ({ trigger }) => ({
    transition: transitions.create('top', {
      easing: transitions.easing.sharp,
    }),
    top: trigger ? 0 : 56,
    zIndex: 1099,
  }),
  searchInput: {
    color: palette.common.white,
    margin: spacing(1),
    padding: spacing(1),
    backgroundColor: 'rgba(255, 255, 255, 0.08)',
  },
  listItemText: {
    letterSpacing: '1px',
    fontWeight: 500,
  },
  subList: {
    backgroundColor: '#f8f8f8',
  },
}));

const AmiMiniHeader = ({ menus,cartLength,isAuth,history ,logout,category}) => {


    const [anchorEl, setAnchorEl] = React.useState(null);


    const isMenuOpen = Boolean(anchorEl);


    function handleProfileMenuOpen(event) {
        console.log(event.currentTarget)
      setAnchorEl(event.currentTarget);
    }



    function handleMenuClose() {
      setAnchorEl(null);

    }

    function handleLogout(){
      document.getElementsByClassName("logout")[0].click()
      logout()
      handleMenuClose()
        history.push('/')
    }


    const menuId = 'primary-search-account-menu';
    const renderMenu = (
      <Menu
        anchorEl={anchorEl}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        id={menuId}
        keepMounted
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={isMenuOpen}
        onClose={handleMenuClose}
      >
       <Link to={'/user/profile'} style={{color:'black'}}> <MenuItem onClick={handleMenuClose}>Profile</MenuItem></Link>

     <Link to={'/user/orders'} style={{color:'black'}}><MenuItem onClick={handleMenuClose}>My Orders</MenuItem></Link>
     <MenuItem onClick={handleLogout}>Logout</MenuItem>
      </Menu>
    );


  const trigger = useScrollTrigger();
  const classes = useStyles({ trigger });
  const [opened, setOpened] = useState(false);
  const [collapsedIndex, setCollapsedIndex] = useState([null, null, null]);
  const updateByIndex = (val, level) => {
    setCollapsedIndex(
      collapsedIndex.map((item, i) => (i === level ? val : item)),
    );
  };
  // eslint-disable-next-line react/prop-types
  const renderItem = (level = 0) => ({ icon, label, children }, index) => {
    const collapsed = collapsedIndex[level];
    const listItemText = (
      <ListItemText
        key={label}
        style={{
          paddingLeft: (level - 1) * 20 + (level === 0 ? 0 : 28),
          marginLeft: level === 0 && !icon ? 28 : 0,
        }}
        classes={{ primary: classes.listItemText }}
        primary={label}
      />
    );
    if (!children) {
      return (
        <>
          <ListItem button key={label}>
            {icon && (
              <Icon size={'small'} push={'right'}>
                {icon}
              </Icon>
            )}
            {listItemText}
          </ListItem>
          {level === 0 && <Divider light />}
        </>
      );
    }
    return (
      <>
        <ListItem
          button
          onClick={() =>
            updateByIndex(collapsed === index ? null : index, level)
          }
        >
          {icon && (
            <Icon size={'small'} push={'right'}>
              {icon}
            </Icon>
          )}
          {listItemText}
          {collapsedIndex[level] === index ? (
            <Icon>expand_less</Icon>
          ) : (
            <Icon>expand_more</Icon>
          )}
        </ListItem>
        <Collapse
          in={collapsedIndex[level] === index}
          timeout="auto"
          unmountOnExit
        >
          <List className={classes.subList} component="div" disablePadding>
            {children.map(renderItem(level + 1))}
          </List>
        </Collapse>
        {level === 0 && <Divider light />}
      </>
    );
  };
  return (
    <>

        <AppBar elevation={1}>
          <Toolbar>
            <Grid container spacing={1} alignItems={'center'}>
              <Grid item xs={2}>
                <IconButton
                  className={'fa fa-bars'}

                  style={{color:'white',paddingRight:'10px'}}
                  onClick={() => setOpened(true)}
                />
              </Grid>
              <Grid item xs={6}>
                <Typography variant={'h6'} weight={600} spacing={'big'} onClick={()=>history.push('/')} inverted>
                  GREEN PLANET
                </Typography>
              </Grid>
              <Grid item xs={2}>
              {isAuth?
                <IconButton
                edge="end"
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                style={{color:'white'}}

              >
                <AccountCircle />
              </IconButton>:




                <NavLink className="nav-link" to={"/login"}><ButtonM style={{color:'white'}}>Login</ButtonM></NavLink>}
              </Grid>
              <Grid item xs={2}>
              <NavLink className="nav-link" to={"/cart"}>  <IconButton style={{color:'white'}} ><Badge  badgeContent={cartLength} color={'secondary'} >  <ShoppingCartIcon/></Badge></IconButton> </NavLink>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>


      <Drawer open={opened} onClose={() => setOpened(false)}>
        <Box minWidth={320}>
          <ListItem>
            <ListItemIcon>
              <IconButton


                onClick={() => setOpened(false)}
              >
<Close/>
              </IconButton>
            </ListItemIcon>
          </ListItem>
          <Divider light />
          <ListItem>
          <CategoryPicker categories={category} />
          </ListItem>
        </Box>
      </Drawer>
        {renderMenu}
    </>
  );
};

AmiMiniHeader.propTypes = {
  menus: PropTypes.arrayOf(
    PropTypes.shape({
      icon: PropTypes.string,
      label: PropTypes.string,
      children: PropTypes.arrayOf(PropTypes.shape({})),
    }),
  ),
};
AmiMiniHeader.defaultProps = {
  menus: [],
};
const mapStateToProps = (state) => {
  return {
      cartLength: state.shop.cart.length,
          isAuth:state.Auth.isAuth,
          
    category: state.common.config.category,
  }
};

export default withRouter(connect(mapStateToProps,{logout})(AmiMiniHeader))
