import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import AddressBlock from '../Uprofile/addressBlock'
import AddressComp  from '../Uprofile/address'
import Button from '@material-ui/core/Button'
class AddressForm extends React.Component  {
  constructor(props) {
    super(props);
    this.state={
      addressDia:false,

    }

      }


  render()
  {
    return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Select Shipping address
      </Typography>
      <AddressBlock mode={'checkOut'}/>
      {!this.state.addressDia&&<Button variant="outlined" fullWidth color="primary" style={{color:'black',padding:'10px',marginTop:'10px'}} fullWidth onClick={()=>this.setState({addressDia:!this.state.addressDia})}  >
      ADD NEW ADDRESS
      </Button>}
      {this.state.addressDia&&<AddressComp mode={1} data={null} index={-1} close={()=>{this.setState({addressDia:!this.state.addressDia})}}/>}

    </React.Fragment>
  );}
}
export default AddressForm
