import React,{Component} from 'react';

import Title from './Title';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../utilities/utilFun'
import {updateAttachment} from './../actions'
import {connect} from 'react-redux';
import {store} from '../App.js'

class CreateProduct extends Component {
  constructor(props) {
    super(props);

    this.waitf=this.waitf.bind(this)
    this.handleProcessed=this.handleProcessed.bind(this)
    this.handleAttach=this.handleAttach.bind(this)
  }




  handleAttach=(e)=>{

        var file=e.target.files[0]
          var type=file.name.split(".")

          console.log(file)
          var id=Date.now()
          var arr=[]
      //  arr=  this.props.attachment
          var arrele={
          id:id,
          status:'queue',
          file:file,
          name:file.name,
          url:'',
          type:type.pop(),
          mime:file.type,
          }
          arr.push({...arrele})

          this.props.updateAttachment(arr)
          console.log(this.props.attachment)
      }

      handleProcessed(resolve){
console.log(resolve)
      var arr=this.props.attachment
      var indexUP=arr.findIndex(
          item => item.id === resolve.id
      );
      arr[indexUP].status='processed';
      arr[indexUP].url=resolve.data.data.Location;
      console.log( arr[indexUP])

      this.props.updateAttachment(arr);
      console.log('processed')
    }




        waitf(value){
console.log('waitif')

    return new Promise(function (resolve, reject){
      UploadEditorImg(value.file,{ext:value.type,mime:value.mime})
      .then((data)=>resolve({id:value.id,data}))



    })
        }


render(){

  var data=[]
  console.log(this.props)
var arr=this.props.attachment
arr.map((value)=>{
  console.log(value)
  if(value.status=='queue'){
    console.log('queue')
      this.waitf(value).then((resolve)=>{this.handleProcessed(resolve)})
      value.status='processing'
}
data.push(value)
})

  return (
    <React.Fragment>


      <form >
      <Grid container spacing={3} justify={'center'}>
      <Grid item xs={12}><Title>Add Product</Title></Grid>

{data.map(value=>{return(<Title>Add Product</Title>)})}
      <Grid item xs={12}><TextField
      id="Title"
      fullWidth
      label="Title of product"
      margin="normal"
      variant="outlined"
      ></TextField></Grid>



      <Grid item xs={12}><TextField
      id="Sku"
      fullWidth
      label="SKU of product"
      margin="normal"
      variant="outlined"
      ></TextField></Grid>




      <Grid item xs={12}>


      <FormControl margin='normal' fullWidth>
             <InputLabel  htmlFor="age-helper" margin='normal'>category</InputLabel>
             <Select
             label='product category'
               margin="normal"

      variant={'outlined'}
               inputProps={{
                 name: 'category',
                 id: 'outlined-age-simple',
               }}
             >
               <MenuItem value="">
                 <em>None</em>
               </MenuItem>
               <MenuItem value={10}>Ten</MenuItem>
               <MenuItem value={20}>Twenty</MenuItem>
               <MenuItem value={30}>Thirty</MenuItem>
             </Select>

             </FormControl>

      </Grid>

      <Grid item xs={12}>
       <InputLabel  htmlFor="age-helper" margin='normal'>Add Images</InputLabel>
      <Grid container spacing={3} justify='center'>
<Grid item xs={6} lg={3} style={{width:'200px',height:'200px',backgroundColor:'grey'}}>
</Grid>
<Grid item xs={6} lg={3} style={{width:'200px',height:'200px',backgroundColor:'grey'}}>
</Grid>
<Grid item xs={6} lg={3} style={{width:'200px',height:'200px',backgroundColor:'grey'}}>
</Grid>
<Grid item xs={6} lg={3} style={{width:'200px',height:'200px',backgroundColor:'grey'}}>
</Grid>


      </Grid>
      </Grid>

      <Grid item xs={12}>
      <TextField
       id="outlined-multiline-static"
       label="description"
       multiline
       rows="4"
       defaultValue="Describe your product here"
fullWidth
       margin="normal"
       variant="outlined"
     />
      </Grid>

      <Grid item xs={6} lg={4}>
      <TextField
      id="Quantity"
      fullWidth
      label="Quantity Available"
      margin="normal"
      type='number'
      variant="outlined"
      ></TextField>
      </Grid>

      <Grid item xs={6} lg={4}>
      <TextField
      id="Price"
      fullWidth
      label="Price of product"
      margin="normal"
      type='number'
      variant="outlined"
      ></TextField>
      </Grid>


      <Grid item xs={6} lg={4}>
      <TextField
      id="PriceVLE"
      fullWidth
      type='number'
      label="Price for VLE"
      margin="normal"
      variant="outlined"
      ></TextField>
      </Grid>


      </Grid>
      </form>

      <input type='file' id='attachupload'  ref="Upload" onChange={this.handleAttach} />
    </React.Fragment>
  );
}

}


const mapStateToProps = state => {

    return {
      attachment:state.admin.attachment
     }
};

export default connect(mapStateToProps,{updateAttachment})(CreateProduct);
