import React,{Component} from 'react';


import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';

import {snackbar} from './../actions'
import {connect} from 'react-redux';

import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import Switch from '@material-ui/core/Switch'

import FormControlLabel from '@material-ui/core/FormControlLabel';
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import Paper from '@material-ui/core/Paper'
import ListItem from '@material-ui/core/ListItem'
import ListSubheader from '@material-ui/core/ListSubheader'
import InputBase from '@material-ui/core/InputBase'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import {

    withRouter
  } from "react-router-dom";
class CreateProduct extends Component {
  constructor(props) {
    super(props);

    this.state={
      text:'',
      editor:null,

    }


  }


render(){



  return (
    <React.Fragment>


<Paper elevation={0} style={{border:'1px solid #f0f0f0',width:'100%'}}>


<List disablePadding={true} dense={true}>
{this.props.specification.map((item,index)=>{
var itemData=[]


  itemData=item.values.map((item2,index2)=>{

    return(

      <ListItem dense divider >
      <Grid container spacing={3}>
      <Grid item xs={6}  >
      <ListItemText style={{color:'grey'}}>
        {item2.key}

      </ListItemText>
      </Grid>
      <Grid item xs={6} >

      <ListItemText>
        {item2.value}

      </ListItemText>

      </Grid>
      </Grid>
    </ListItem>

    )
  })
  itemData.splice(0,0,

    <ListItem style={{background:'rgba(0,0,0,0.1)',borderBottom:'1px solid #f0f0f0'}}>
  <ListSubheader style={{background:'rgba(0,0,0,0)'}} id={index}>{item.title}</ListSubheader>


</ListItem>
  )
return(itemData)
})}


</List>
</Paper>
    </React.Fragment>
  );
}

}



export default withRouter((CreateProduct));
