import React ,{Component} from 'react';
import {connect} from 'react-redux';
import {formatMoney} from "../../pipes/priceFormatter";
import CartItem from "../../components/CartItem/CartItem";
import Button from "@material-ui/core/Button"
import { withRouter } from "react-router-dom";
import Hidden from "@material-ui/core/Hidden";
import axios from 'axios';
import {setCart} from '../../actions'
class ShoppingCart extends Component {
  constructor(props) {
          super(props);
      }

componentDidMount=()=>{
  console.log(this.props)
  if(this.props.Auth.isAuth){
    axios.post('https://green-planet.herokuapp.com/public/user/getCart',{_id:this.props.Auth.user._id}).then((res)=>{
      console.log(res.data)
if(res.data.data.cart){
  console.log("got")
  this.props.setCart(res.data.data.cart)
}

    })
  }

}


render(){


var oos=false
console.log(oos)

    return (
        <>

                <div className="container" style={{paddingTop: '60px'}}>
                    <div className="card shopping-cart" style={{minWidth:'80vw'}}>
                        <div className="card-header  ">
                            <i className="fa fa-shopping-cart pr-2" style={{color:'black'}} aria-hidden="true"></i>
                            Shipping cart
                            <div className="clearfix"></div>
                        </div>
                        <div className="card-body">
                            {this.props.cartItemCount ? this.props.cartItems.map(cart => {

                                     if(cart.product.quantity<cart.quantity)
                                     oos=true;
                              return(
                                <CartItem {...cart.product } VLE={this.props.Auth.authType=='VLE'?true:false} quant={cart.quantity}  />
                            )}) : <h3 className=" text-center">There is no product in your cart</h3> }
                        </div>
                        <div className="card-footer">
                    {    oos&&  <div className="pull-left" style={{margin: '10px'}}>One or more item in unavailable</div>}
                            <div className="pull-right" style={{margin: '10px'}}>
                                <div className="pull-right" style={{margin: '5px'}}>
                                   <p style={{textAlign:'right'}}>Total price: <i class="fa fa-inr"></i> <b>{formatMoney(this.props.totalPrice)}</b></p>

                                    <br/>
                                    <br/>
                                    <Hidden smDown>
                                    <Button variant="contained" color="primary" style={{color:'white',padding:'10px',paddingRight:'50px',paddingLeft:'50px',marginRight:'20px'}} onClick={()=>{this.props.history.push('/products')}} >
        CONTINUE SHOPPING
      </Button>
                                  {this.props.cartItemCount>0&&!oos&&   <Button variant="contained" color="primary" style={{color:'white',padding:'10px',paddingRight:'50px',paddingLeft:'50px'}} onClick={()=>this.props.history.push('/user/checkout')} >
        CHECKOUT
      </Button>}
      </Hidden>
      <Hidden smUp>
                                    <Button variant="contained" color="primary" style={{color:'white',padding:'10px',width:'90%',margin:'10px'}} onClick={()=>{this.props.history.push('/products')}} >
        CONTINUE SHOPPING
      </Button>
                                  {this.props.cartItemCount>0&&!oos&&  <Button variant="contained" color="secondary" style={{color:'white',padding:'10px',width:'90%',margin:'10px'}} onClick={()=>this.props.history.push('/user/checkout')} >
        CHECKOUT
      </Button>}
      </Hidden>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </>
    );
  }
};


const mapStateToProps = state => {

    console.log(state, 'state has changed');

    return {
      Auth:state.Auth,
        cartItems: state.shop.cart,
        cartItemCount: state.shop.cart.reduce((count, curItem) => {
            return count + curItem.quantity;
        }, 0),
        totalPrice: state.shop.cart.reduce((count, curItem) => {
          if(state.Auth.authType=='VLE')
            return count + (curItem.product.pricing.priceVLE * curItem.quantity);
            else {
              return count + (curItem.product.pricing.price * curItem.quantity);
            }
        }, 0)
    }
}

export default withRouter(connect(mapStateToProps, {setCart})(ShoppingCart));
