import React, { Component } from 'react';
import {Provider} from 'react-redux';
import {createStore } from 'redux';
import rootReducer from './reducers';
import userOrder from './Amigo/yourOrders'
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom';
import {getCart} from './actions/sync'
import './App.scss';
import theme from './Amigo/theme';
import Rating from './Amigo/RRproduct'
import Home from "./pages/Home/Home";
import Header from "./components/Header/Header";
import Snackbar from "./components/message/snackbar"
import ProductDetail from "./Amigo/Detail";
import ShoppingCart from "./Amigo/AmigoCart";
import Login from "./components/Auth/login"
import UserProfile from './Amigo/profile'
import axios from 'axios'
import Checkout from './Amigo/checkout'
import register from './components/Auth/register'
import ResetPass from './components/Auth/forgotpass'
import AddProduct from './adminPages/createProduct'
import Script from 'react-load-script'
import Invoice from './components/pdfInvoice/test'
import CategoryPage from './Amigo/AmigoCategory'
import { ThemeProvider } from '@material-ui/styles';
import Footer from './Amigo/footer'
import TreeView from './components/Tabs/treeview'
export const  store = createStore(rootReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());
export const stateAll=store.getState()
class App extends Component {

componentWillMount=()=>{

if(localStorage.Auth)
  {var sess=JSON.parse(localStorage.Auth)
  console.log(sess)
  if(sess!=null)
  store.dispatch({type: 'SET_USER', data:{user: sess.user,token:sess.token}})}
var stateAll=store.getState()
console.log(stateAll)
  if(stateAll.Auth.isAuth){
  getCart(stateAll.Auth.user._id)
  }

  axios.post('https://green-planet.herokuapp.com/public/config/get',{}).then(res=>{
    if(res.data){
      console.log(res.data[0])
      store.dispatch({type:'SETCONFIG',config:{...res.data[0]}})
      this.setState({...res.data[0]})
    }
  })
}

  render() {


    return (
        <Provider store={store}>
          <ThemeProvider theme={theme}>
            <BrowserRouter>
            <React.Fragment>
              <Script url={'https://sboxcheckout-static.citruspay.com/bolt/run/bolt.min.js'} attributes={{id:'bolt'}}  onLoad={()=>console.log('added')}/>
               <Snackbar/>
                <Switch>
                    <Route exact path={'/'} render={() => {
                        return <Redirect to={'/products'}/>
                    }}/>
                    <Route exact path={'/products'} component={Home}/>
                    <Route exact path={'/rating/:productId'} component={Rating}/>
                    <Route exact path={'/products/:id'} component={ProductDetail}/>
                        <Route exact path={'/products/category/:category'} component={CategoryPage}/>
                    <Route exact path={'/cart'} component={ShoppingCart}/>
                    <Route exact path={'/login'} component={Login}/>
                        <Route exact path={'/register'} component={register}/>
                          <Route exact path={'/resetPassword'} component={ResetPass}/>
                    <Route exact path={'/addProduct'} component={AddProduct}/>
                    <Route exact path={'/user/orders'} component={userOrder}/>
                    <Route exact path={'/user/profile'} component={UserProfile}/>
                    <Route exact path={'/user/checkout'} component={Checkout}/>
                    <Route exact path={'/tree'} component={TreeView}/>
                                        <Route exact path={'/util/invoice'} component={Invoice}/>
                </Switch>
<Footer/>
            </React.Fragment>
            </BrowserRouter>
          </ThemeProvider >
        </Provider>
    );
  }
}

export default App;
