
import React, { Component }  from 'react';
import PropTypes from 'prop-types';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import withStyles from '@material-ui/core/styles/withStyles';
import { connect } from 'react-redux'
import {GoogleLogin} from 'react-google-login'
import Hidden from '@material-ui/core/Hidden'
import axios from 'axios'
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Container from '@material-ui/core/Container';
import {getCart} from '../../actions/sync'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import OtpInput from 'react-otp-input';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";

import {
   SignInUser,
   snackbar
 } from './../../actions/index'
const styles = theme => ({

   paper: {
     marginTop: theme.spacing(8),
     display: 'flex',
     flexDirection: 'column',
     alignItems: 'center',
   },
   avatar: {
     margin: theme.spacing(1),
     backgroundColor: theme.palette.secondary.main,
   },
   form: {
     width: '100%', // Fix IE 11 issue.
     marginTop: theme.spacing(1),
   },
   submit: {
     margin: theme.spacing(3, 0, 2),
   },
});



    class SignIn extends Component {
        constructor(props) {
                super(props);
                this.state={
                  mobile:'',
                  password:'',

                }
            }

            componentDidMount=()=>{
              if(this.props.isAuth)
              this.props.history.push('/')
            }

setOtp=(otp)=>{
  var phoneno = /^\d{1,4}$/;

  if((otp.match(phoneno)&&otp.length<11))
        {
  this.setState({otp:this.state.otp+otp})
}
}


handleChange=(e)=>{
  var phoneno = /^\d{1,10}$/;
  console.log(e.target.value)
  if((e.target.value.match(phoneno)&&e.target.value.length<11))
        {
          this.setState({mobile:e.target.value})
          console.log(this.state.mobile)
        }
  if(e.target.value==''){
    this.setState({mobile:''})
  }

}


        responseGoogle = (res) => {
            console.log(res)
            axios.post(`https://green-planet.herokuapp.com/public/user`,{token:res.tokenId}).then((res)=>{
                console.log(res.data)
                if(res.data.type=='error')
                {
                    this.props.snackbar({open:true,sType:'error',message:'Login Failed!Please try again',})
                }
                else if(res.data.type!=='error'&& res.data.type){
                let user = res.data.data

                console.log('==================signin=======')
                console.log(res.data)
                console.log('==================signin=======')
                localStorage.setItem('Auth', JSON.stringify({user:user,token:res.data.token}))
  this.props.SignInUser({token:res.tokenId,user:user})
  getCart(user._id)
  this.props.snackbar({sType:'success',open:true,message:'Logged in to green planet '})
        console.log(res)
                }
          })}



handleMobileLogin=()=>{
  if(this.state.mobile.length<10){
    this.props.snackbar({sType:'error',open:true,message:'Please enter valid mobile number'})
  }else
  if(this.state.password.length<4){
      this.props.snackbar({sType:'error',open:true,message:'Please enter valid password'})
  }
  else{
      this.props.snackbar({sType:'info',open:true,message:'Please wait...'})
    axios.post('https://green-planet.herokuapp.com/public/user/mobileLogin',{password:this.state.password,uid:this.state.mobile}).then(res=>{
      if(res.data.type=='error')
      {
          this.props.snackbar({open:true,sType:'error',message:'Login Failed!Please try again',})
      }
      else
      if(res.data.type=='incorrect'){
          this.props.snackbar({open:true,sType:'error',message:'incorrect password',})
      }
      else
      if(res.data.type=='not exist'){
          this.props.snackbar({open:true,sType:'error',message:'User account not exists',})
      }
      else if(res.data.type=='success'&& res.data.type){
        let user = res.data.data

        console.log('==================signin=======')
        console.log(res.data)
        console.log('==================signin=======')
        localStorage.setItem('Auth', JSON.stringify({user:user,token:res.data.token}))
this.props.SignInUser({token:res.tokenId,user:user})
getCart(user._id)
this.props.snackbar({sType:'success',open:true,message:'Logged in to green planet '})
      }
    })
  }


}


          render() {
              //if(this.props.isAuth){
               //   this.props.history.push('/')
             // }
             if(this.props.isAuth)
             this.props.history.push('/')
const { classes } = this.props;

return (



  <Container component="main" maxWidth="xs">
     <CssBaseline />
     <div className={classes.paper}>
       <Avatar className={classes.avatar}>
         <LockOutlinedIcon />
       </Avatar>
       <Typography component="h1" variant="h5">
         Sign in
       </Typography>
       <form className={classes.form} noValidate>
         <TextField
           variant="outlined"
           margin="normal"
           required
           fullWidth
           id="mobile"
           value={this.state.mobile}
           label="Mobile Number"
           name="mobile"
           onChange={this.handleChange}
           autoComplete="mobile"
           autoFocus
         />
         <TextField
           variant="outlined"
           margin="normal"
           required
           fullWidth
           value={this.state.password}
           onChange={(e)=>this.setState({password:e.target.value})}
           name="password"
           label="Password"
           type="password"
           id="password"
           autoComplete="current-password"
         />
         <FormControlLabel
           control={<Checkbox value="remember" color="primary" />}
           label="Remember me"
         />
         <Button

           fullWidth
           variant="contained"
           color="primary"
           onClick={this.handleMobileLogin}
           className={classes.submit}
         >
           Sign In
         </Button>
         <Button
            
             fullWidth
             variant="contained"
             color="primary"
             className={classes.submit}
             onClick={()=>{var x=document.getElementsByClassName("google")[0].click()}}
           >
             Sign in with Google
           </Button>
         <Grid container>
           <Grid item xs>
             <Link to={'/resetPassword'} variant="body2">
               Forgot password?
             </Link>
           </Grid>
           <Grid item>
             <Link to={'/register'} variant="body2">
               {"Don't have an account? Sign Up"}
             </Link>
           </Grid>
         </Grid>
       </form>
     </div>
     <Box mt={8}>
       <Hidden xsUp implementation="css">
       <GoogleLogin className="button google"
                       clientId="143719263118-jmhigagqibb2ec9hj2p8b1ospkfbe844.apps.googleusercontent.com"
                       onSuccess={this.responseGoogle}
                       onFailure={this.responseGoogle} >
                           <i className="fa fa-google"></i><span> SignIn with Google</span>
                       </GoogleLogin></Hidden>
     </Box>
   </Container>




);
          }
}

SignIn.propTypes = {
classes: PropTypes.object.isRequired,
};
const mapStateToProps = state => {
    return {

        isAuth: state.Auth.isAuth
    }
}


export default withRouter(connect(mapStateToProps, {

    SignInUser,
    snackbar

})(withStyles(styles)(SignIn)));
